<!-- _template_WaybillInfo.vue    -->
<template>
  <div class="_template_WaybillInfo">
	<div  class="bigList" v-for='(dataItem,index) in shipmentListData'  :id="index"  :key='index'  >
	  <div  class="listHead"  @click="Details_waybill(dataItem.shipmentID,index,LinkStatus)" >
		<!--  -->
		<span   @click="Navigation(index)" class="Navigation"></span>
		<!--  -->
		<span v-if='dataItem.status' class="LicensePlate">{{dataItem.status}}</span>
		<!--  -->
		<div  class="firstLine">
		  <!-- <span>{{dataItem.startCity}}</span> -->
		  <i class="iconfont" ></i>
		  <!--  icon-jiang -->
		  <!-- <span>{{dataItem.endCity}}</span> -->
		</div>
		<!--  -->
		<div class="codestyle">
		  <span></span>
		  <span @click='stopevent' v-clipboard:copy="dataItem.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError">{{dataItem.shipmentCode}}</span>&nbsp;&nbsp;
		  <i  @click='stopevent'  v-clipboard:copy="dataItem.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>&nbsp;&nbsp;
		  <i @click="_ewm(dataItem.shipmentCode)" class="iconfont icon-iconfonterweima" ></i>
		  <!-- <span class="BackBottleClass" v-if="dataItem.backBottleFlag == 'T'"></span> -->
		</div>
		<div>
		  <span>&nbsp;&nbsp;&nbsp;</span>
		  <span>{{dataItem.countDelivery}}  &nbsp; </span>
		  <span>{{(dataItem.vol * 0.000001).toFixed(1)}}  &nbsp; </span>
		  <span>{{(dataItem.wt * 0.000001).toFixed(1)}}  &nbsp; </span>
		</div>
		<div>
		  <div>
			<span></span>
			<span>{{dataItem.transPartyName}}</span>
		  </div>
		</div>
		<!-- <div><span>  &nbsp;</span><span>{{dataItem.receivePartyName}}</span></div> -->
		<div v-if="dataItem.issueAddrLiat.length==1">
		  <span></span>
		  <span>{{dataItem.issueAddrLiat[0].issueAddr1}}</span>
		</div>
		<div v-if="dataItem.issueAddrLiat.length==1">
		  <span>  &nbsp;</span>
		  <span>{{dataItem.issueAddrLiat[0].issueUser}}&nbsp;&nbsp;&nbsp; {{dataItem.issueAddrLiat[0].issuePhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i v-if="dataItem.issueAddrLiat[0].issuePhone" @click="callPhone(dataItem.issueAddrLiat[0].issuePhone)" class="iconfont icon-dianhua-copy" ></i>
		</div>
		<div  v-if="dataItem.issueAddrLiat.length !=1 &&dataItem.issueAddrLiat.length !=0">
		  <i class="iconfont icon-tishi" ></i>
		  <span  @click="MultiAddress(index)" style="color:#038EEA; "></span>
		</div>
	<!-- 	<div v-if="dataItem.issueAddrLiat.length == 1 && dataItem.issueAddrLiat[0].phoneAppointTime">
		  <span></span>
		  <span>{{dataItem.issueAddrLiat[0].phoneAppointTime}}</span>
		</div>
		<div v-if="dataItem.issueAddrLiat.length == 1 && dataItem.issueAddrLiat[0].phoneAppointTime">
		  <span></span>
		  <span>{{dataItem.issueAddrLiat[0].appointLoadingStatus == "NEW" ? "": dataItem.issueAddrLiat[0].appointLoadingStatus == "YI" ? "" : dataItem.issueAddrLiat[0].appointLoadingStatus == "REFUSE" ? "" : ""}}</span>
		</div> -->
		<!-- // appointLoadingStatus;//NEW=YI=REFUSE= -->
	  </div>
	  <p>
		<!--  -->
		<!-- <span v-if="LinkStatus == 'Link_PickUp'"  @click="toDeliveryScanXH(index)"></span> -->

		<!--   -->
		<span v-if="LinkStatus == 'Link_PickUp' || LinkStatus == 'Link_Transit'"  @click="SubmitShipment_PickUp(index,'')"></span>

		<!--   -->
		<!-- <span v-if="LinkStatus == 'Link_Transit'"  @click="toTransitScan(index)"></span> -->
		
		<!--   -->
		<span v-if="LinkStatus == 'Link_ToSend' && dataItem.loadingStatus == 'WEI'"  @click="SubmitShipment_PickUp(index,'')"></span>

		<!--   -->
		<!-- <span v-if="LinkStatus == 'Link_ToSend' && dataItem.loadingStatus == 'YI'"  @click="SubmitShipment_PickUp(index,'')"></span> -->

		<!--  -->
		<!-- <span v-if="LinkStatus == 'Link_ToSend'"  @click="toReceiptScan(index)"></span> -->

		<!--  -->
		<!-- <span v-if="LinkStatus == 'Link_PickUp' || LinkStatus == 'Link_Transit'"  @click="toMassTransfer(index)"></span> -->

		<!--  -->
		<span v-if="LinkStatus == 'Link_Transit'"  @click="toAbnormal(index)"></span>

		<!--  -->
		<!-- <span  @click="toTrackOnTheWay(index)"></span> -->

	  </p>
	  <!-- <p>
	  	
	  		<span  v-if="LinkStatus == 'Link_ToSend'"  @click="toMassTransfer(index)"></span>
	  		<span  v-if="LinkStatus == 'Link_ToSend'"  @click="toMassTransfer(index)"></span>
	  </p> -->
	</div>
	<!--  -->
	<div class="MultiAddress">
	  <div>
		<i @click="closeAddress"  class="iconfont icon-iconfontcuowu" ></i>
		<div class="addressHead">
		  <span  @click="address_SwitchView(idx)"  v-for='(itemAddHead,idx) in address_listData'  :id="idx"  :key='idx' >{{idx+1}}</span>
		</div>
		<div class="addressContent">
		  <div>
		  	<span></span>
		  	<span>{{currentAddressInfo.issueAddr1}}</span>
		  </div>
		  <div>
		  	<span></span>
		  	<span>{{currentAddressInfo.issueUser}}</span>
		  </div>
		  <div>
		  	<span></span>
 		  	<span>{{currentAddressInfo.issuePhone}}</span> &nbsp;&nbsp;&nbsp;&nbsp;  <i v-if="currentAddressInfo.issuePhone" style="color:#038EEA; " @click="callPhone(currentAddressInfo.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
		  </div>
		  <div v-if="currentAddressInfo.phoneAppointTime">
		  	<span></span>
		  	<span>{{currentAddressInfo.phoneAppointTime}}</span>
		  </div>
		  <div v-if="currentAddressInfo.phoneAppointTime">
		  	<span></span>
		  	<span>{{currentAddressInfo.appointLoadingStatus}}</span>
		  </div>
		</div>
	  </div>
	</div>
	<!--  -->
	<div class="Navigate_MultiAddress" v-if="typeMShow">
	  <div>
		<div class="SelectTitle"></div>
		<div class="SelectContent">
		  <div class="addressContent" @click="Navigate_SelectComit(itemAddress.issueAddr1)"  v-for='(itemAddress,idx) in Navigate_addressList'  :id="idx"  :key='idx'>
			<div>
			  <span>{{itemAddress.issueAddr1}}</span>
			</div>
			<div>
			  <span>{{itemAddress.issueUser}} &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span><span>{{itemAddress.issuePhone}}</span><i style="color:#038EEA;" @click="callPhone(itemAddress.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
			</div>
		  </div>
		</div>
		<div class="cancel"   @click="selectCancel('e')"></div>
	  </div>
	</div>
	<div v-if="ifTips" class="msg_tips msg_tips1">
		<div class="tips_content">
			<i class="iconfont icon-chenggong1"></i>
			<span>{{tips_Msg}}</span>
		</div>
	</div>
  </div>
</template>

<script>
import $ from "jquery"

export default {
  name:"WaybillInfo_template",
  data(){
    return{
      Exceed:false,//
      currentAddressInfo:{},//
      address_listData:[],// shipmentListData[i].issueAddrLiat
      typeMShow:false,//
      Navigate_addressList:[],//
      DriverIssueShow:false,//  
      DriverReceiveShow:false,//  
      CurrentLocation:'',//
	  shipmentIdx:0,//idx
	  confirmNumOfDriverIssue:'',//
	  confirmNumOfDriverReceive:'',//
	  tips_Msg:"",//
	  ifTips:false
    }
  },
  props:{
	shipmentListData:{
	},
	LinkStatus:'',//
  },
  created(){
    var that = this;
  },
  methods:{
	SetCurrAddress:function(address) {

		if(address == "") {

			this.$alert('', '', {
        		confirmButtonText: '',
        		callback: action => {
        		}
   			})
		}else {

			this.CurrentLocation = address;
		}
	},
	// 
	_ewm(shipmentCode){
		event.stopPropagation();
		var that = this;

		this.$route.query.LinkStatus = this.LinkStatus;

		console.log(this.$route.query.LinkStatus)

		this.$router.push({
			name:"QRCode",
			query:{
				shipmentCode:shipmentCode,//
				LinkStatus:that.LinkStatus,//
			}
		})
	},
	//   
	// LoadingAppoint  (index){
	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};

	// 	var that = this;

	// 	this.$router.push({
	// 		name:"LoadingAppoint",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	//   
	// toArrivalConfirm  (index){

	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};

	// 	var that = this;

	// 	this.$router.push({
	// 		name:"ArrivalConfirm",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	//   
	// toWayFeedback  (index){

	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};

	// 	var that = this;

	// 	this.$router.push({
	// 		name:"WayFeedback",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	//   
	toDeliveryScanXH (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"DeliveryScanXH",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	//   
	SubmitShipment_PickUp(index,text){
		event.stopPropagation();
		
		var that = this;

		this.$confirm(""+text+" "+ that.shipmentListData[index].shipmentCode , '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {

        	var linkNode = ''; // linkNode

	        if(that.LinkStatus == 'Link_PickUp'){
	        	linkNode = '10';
	        }
	        if(that.LinkStatus == 'Link_Transit'){
	        	linkNode = '20';
	        }
	        if(that.LinkStatus == 'Link_ToSend' && that.shipmentListData[index].loadingStatus == 'WEI'){
	        	linkNode = '30';
	        }
	        if(that.LinkStatus == 'Link_ToSend' && that.shipmentListData[index].loadingStatus == 'YI'){
	        	linkNode = '40';
	        }

        	var postData = {
				shipmentCode:that.shipmentListData[index].shipmentCode,//
				transportLinks:linkNode,//
				driverName:that.$store.state.userInfo.userName,//
			}

			console.log(postData)

			that.httpRequest( "updateDeliveryScanNumber.do",postData,function(res){
				console.log(res)


	          that.$alert('!', '', {
	            confirmButtonText: '',
	            callback(){
	              that.$parent.loadData();
	            }
	          })
			},"isLoading_")
          
        }).catch(() => {
          // this.$message({
          //   type: 'info',
          //   message: ''
          // });          
        });

		// that.$alert(" "+ that.shipmentListData[index].shipmentCode , '', {
  //           confirmButtonText: '',

  //           callback: action => {
	            
  //           }
  //      	})

	},
	//   
	toTransitScan (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"TransitScan",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	//   
	toMassTransfer (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"MassTransfer",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	//   
	toAbnormal (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"Abnormal",
			query:{
				shipmentCode : that.shipmentListData[index].shipmentCode
			}
		})
	},
	//   
	toTrackOnTheWay (index){

		event.stopPropagation();
		
		var that = this;

		this.$router.push({
			name:"TrackOnTheWay",
			query:{
				shipmentID : that.shipmentListData[index].shipmentID
			}
		})
	},
	// //   
	// toDepartureConfirm  (index){

	// 	event.stopPropagation();

	// 	if(this.DriverError(index) == 10){
	// 		return
	// 	};
		
	// 	var that = this;

	// 	this.$router.push({
	// 		name:"DepartureConfirm",
	// 		query:{
	// 			StowageList : that.shipmentListData[index]
	// 		}
	// 	})
	// },
	// 
	Details_waybill(shipmentID,index){

		var that = this;
		if(this.$route.name == 'WaybillInfo'){
			return;
		}
		if(this.DriverError(index) == 10){
			return
		};

		this.$store.state.Waybill.WaybillInfo.clickWaybillInfo = [this.shipmentListData[index]];

		this.$store.state.Waybill.WaybillInfo.clickShipmentId = shipmentID;

		this.$router.push({
            name:"WaybillInfo",
            query:{
            	LinkStatus:that.LinkStatus
            }
        })
	},
	//   	
	//     
	DriverError(index){

		var that = this;

		var thisShipment = that.shipmentListData[index];

		// 
		if(thisShipment.countDelivery == 'F'){

			that.$alert('', '', {
	            confirmButtonText: '',
	            callback: action => {
        
		           	if(that.$route.name == 'Waybill'){

	            		that.$emit('fatherMethodloadData');

	            	}else{
	            		that.$router.push({
				            name:"WaybillInfo",
				        })
	            	}
	            }
           	})
           	return 10;
		}
	},
	onCopy (){

		var that = this;
		that.ifTips = true;
		that.tips_Msg = "";

		setTimeout(function(){

			that.ifTips = false;
		},1000)
	},
  }
}
</script>

<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .bigList{
  	background-color:#fff;
	border-radius: 20/50rem;
	text-align: left;
	margin-bottom: 15/50rem;
	overflow: hidden;
	&>p{
		margin:20/50rem 20/50rem;
		display: flex;
		justify-content :  flex-end;
		span{
			flex-grow:1;
			max-width: 32%;
			margin-left: 15/50rem;
			text-align: center;
			background-color: @PublicColor;
			color: #fff;
			border-radius: 15/50rem;
			padding: 8/50rem 0;
		}
		span:nth-child(1){
			margin-left:0;
		}
	}
  }
  .listHead{
	overflow: hidden;
	position: relative;
	width: 100%;
	padding: 20/50rem;
	padding-bottom: 0;
	// background-color: #F4F4F4;
	box-sizing: border-box;
	&>div{
		line-height: 55/50rem;
		overflow: hidden;
		&>div{
			float: left;
		}
		&.firstLine{
			width: 600/50rem;
			span{
				float: left;
				&:nth-child(1){
					margin-left: 180/50rem;
				}
			}
			.icon-jiang{
				margin:0 15/50rem;
				float: left;
			}
		}
		&.codestyle{
			overflow: hidden;
			span{
				float: left;
			}
			i{
				float: left;
				margin: 0 15/50rem;
			}
		}
		&.mileage{
			&>div{
				width: 355/50rem;
			}
		}
	}
	.iconfont{
		font-size: 35/50rem;
		color: @PublicColor;
		margin-right: 5/50rem;
	}
	.LicensePlate{
		position: absolute;
		top: 20/50rem;
		left: 0;
		background-color: @PublicColor;
		padding:0  25/50rem;
		height: 50/50rem;
		line-height: 50/50rem;
		color: #fff;
		border-radius: 0 25/50rem 25/50rem 0;
	}
	.Navigation{
		position: absolute;
		right: 20/50rem;
		top: 20/50rem;
		width:100/50rem;
		height: 100/50rem;
		line-height: 100/50rem;
		background-color: @PublicColor;
		border-radius: 50%;
		text-align: center;
		color: #fff;
	}
	.BackBottleClass{
		width: 120/50rem;
		text-align: center;
		color: #fff;
		margin-top: 5/50rem;
		height: 45/50rem;
		line-height: 45/50rem;
		background-color:#00C800;
		border-radius:25/50rem ;
	}
  }
  .confirmNumOfDriverIssue,.confirmNumOfDriverReceive{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left:0;
    background-color: rgba(0,0,0,0.5);
    z-index: 1000;
    text-align: left;
    ._DriverIssue_container{
	    width: 650/50rem;
	    padding:0 20/50rem 20/50rem;
	    box-sizing:border-box;
	    background-color: #fff;
	    position: absolute;
	    top: 50%;
	    left: 50%;
	    font-size: 32/50rem;
	    margin-top: -403/50rem;
	    margin-left: -325/50rem;
	    border-radius: 10/50rem;
	    &>.icon-iconfontcuowu{
	        position: absolute;
	        top: -10/50rem;
	        right: -10/50rem;
	        font-size: 55/50rem;
	    }
	    &>._DriverIssue_head{
	    	height: 90/50rem;
	    	line-height: 90/50rem;
	    	font-weight: 600;
	    	text-align: center;
	    	font-size: 37/50rem;
	    }
	    &>._DriverIssue_btn{
	    	width: 100%;
	    	box-sizing:border-box;
	    	display: flex;
	    	margin-top: 20/50rem;
	    	span{
	    		flex-grow: 1;
	    		height: 70/50rem;
	    		line-height: 70/50rem;
	    		background-color:@PublicColor;
	    		color: #fff;
	    		border-radius:8/50rem;
	    		text-align: center;
	    		margin-right: 30/50rem;
	    	}
	    	span:last-child{
	    		margin-right: 0;
	    	}
	    }
	}
  }
  // 
  .confirmNumOfDriverIssue ._DriverIssue_container ._DriverIssue_content{
	div._content_DriverIssue_code{
			line-height: 50/50rem;
	}
	div._content_DriverIssue_List{
		overflow: scroll;
		max-height: 600/50rem;
		&>div:first-child{
			line-height: 65/50rem;
		}
	}
	div.DriverIssue_detaiList{
		color: #777;
		margin-bottom: 15/50rem;
		margin-top: 5/50rem;
		div{
			line-height: 45/50rem;
		}
	}
  }
  // 
  .confirmNumOfDriverReceive ._DriverIssue_container ._DriverIssue_content{
	div._content_DriverIssue_code{
		line-height: 50/50rem;
	}
	div._content_DriverIssue_List{
		overflow: scroll;
		max-height: 600/50rem;
		color: #777;
		div.DriverIssue_detaiList{
			margin-top: 5/50rem;
			margin-bottom: 15/50rem;
			&>div:nth-child(1){
				color: @PublicColor;
			}
		}
		div.DriverReceivedamage{
			color: #F01418;
		}
	}
  }
</style>



