<template>
	<div class="Reg">
		<header><i class="iconfont icon-xiangzuo1"  @click="goPrev"></i><span></span></header>
		<div class="container">
			<div class='regContainer'>
			  	<div class='regInfo'>
			  		<p><span></span></p>
			    	<div><span></span><input  readonly  v-model='cellphone'></input></div>
			    	<div><span></span><input placeholder=''   ref="userName"   @keyup.enter="getFocus('password')"     v-model='userName'></input><i v-if="userName" @click="clearInput('userName')" class="iconfont icon-iconfontcuowu"></i></div>
			    	<div><span></span><input placeholder=''   ref="password"   @keyup.enter="getFocus('IDCardNum')"    v-model='password'></input><i v-if="password" @click="clearInput('password')" class="iconfont icon-iconfontcuowu"></i></div>
			    	<div><span></span><input placeholder=''   ref="IDCardNum"   @keyup.enter="getFocus('vicheNo')"    v-model='IDCardNum'></input><i v-if="IDCardNum" @click="clearInput('IDCardNum')" class="iconfont icon-iconfontcuowu"></i></div>

			    	<p><span></span></p>
			    	<div><span></span><input placeholder=''   ref="vicheNo"   @keyup.enter="getFocus('vehicleLength')"    v-model='vicheNo'></input><i v-if="vicheNo" @click="clearInput('vicheNo')" class="iconfont icon-iconfontcuowu"></i></div>
				    <div @click='TypeOfCar'><span></span><div><span>{{vehicleType}}</span><i class="iconfont icon-xiangshang"></i></div></div>
				    <div><span></span><input placeholder='' type="number"   ref="vehicleLength"   @keyup.enter="getFocus('vehicleWide')"    v-model='vehicleLength'></input><i v-if="vehicleLength.length" @click="clearInput('vehicleLength')" class="iconfont icon-iconfontcuowu"></i></div>
				    <div><span></span><input placeholder=''  type="number"    ref="vehicleWide"   @keyup.enter="getFocus('vehicleHeight')"    v-model='vehicleWide'></input><i v-if="vehicleWide.length" @click="clearInput('vehicleWide')" class="iconfont icon-iconfontcuowu"></i></div>
				    <div><span></span><input placeholder=''  type="number"   ref="vehicleHeight"   @keyup.enter="getFocus('maxLoadWeight')"    v-model='vehicleHeight'></input><i v-if="vehicleHeight.length" @click="clearInput('vehicleHeight')" class="iconfont icon-iconfontcuowu"></i></div>
				    <div><span></span><input placeholder=''  type="number"    ref="maxLoadWeight"   @keyup.enter="getFocus('maxLoadVolumn')"    v-model='maxLoadWeight'></input><i v-if="maxLoadWeight.length" @click="clearInput('maxLoadWeight')"  class="iconfont icon-iconfontcuowu"></i></div>
				    <div><span>()</span><input placeholder='' type="number"   ref="maxLoadVolumn"   @keyup.enter="regBtn"    v-model='maxLoadVolumn'></input><i v-if="maxLoadVolumn" @click="clearInput('maxLoadVolumn')" class="iconfont icon-iconfontcuowu"></i></div>
				
					<p><span></span></p>
					<div class="uploadImg">
						<div class="LoadImg_head"></div>
						<div>
							<el-upload  :action="HOST+'registerUploadPicture.do'"  :limit="2" :multiple="true"  :data="uploadParams" ref='upload_info1' :auto-upload="false" list-type="picture-card"  :before-upload="beforeAvatarUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
							  <i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
							  <img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</div>
					</div>
					<div class="uploadImg">
						<div class="LoadImg_head"></div>
						<div>
							<el-upload  :action="HOST+'registerUploadPicture.do'"   :limit="2" :multiple="true"  :data="uploadParams" ref='upload_info2' :auto-upload="false" list-type="picture-card"  :before-upload="beforeAvatarUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
							  <i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
							  <img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</div>
					</div>
					<div class="uploadImg">
						<div class="LoadImg_head"></div>
						<div>
							<el-upload  :action="HOST+'registerUploadPicture.do'"   :limit="3" :multiple="true"  :data="uploadParams" ref='upload_info3' :auto-upload="false" list-type="picture-card"  :before-upload="beforeAvatarUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
							  <i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
							  <img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</div>
					</div>
					<div class="uploadImg">
						<div class="LoadImg_head"></div>
						<div>
							<el-upload  :action="HOST+'registerUploadPicture.do'"   :limit="3" :multiple="true"  :data="uploadParams" ref='upload_info4' :auto-upload="false" list-type="picture-card"  :before-upload="beforeAvatarUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
							  <i class="el-icon-plus"></i>
							</el-upload>
							<el-dialog :visible.sync="dialogVisible">
							  <img width="100%" :src="dialogImageUrl" alt="">
							</el-dialog>
						</div>
					</div>
			  	</div>
			  	<div class="g_signBtn" @click='regBtn'></div>
			</div>
			<div v-if="ifTips" class="msg_tips">
				<div class="tips_content">
					<i class="iconfont icon-chenggong1"></i>
					<p>{{tips_Msg}}</p>
				</div>
			</div>
			<div class="SelectMeng"   v-if="typeMShow">
				<div class="SelectWarehouses" >
					<div class="SelectTitle"></div>
					<div class="SelectItem">
						<div @click="SelectComit(index)"  v-for='(dataItem,index) in typeOfCarList'  :id="index"  :key='index'>{{dataItem.description}}</div>
					</div>
					<div class="cancel"   @click="selectCancel('e')"></div>
				</div>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"Reg",
		data(){
			return{
				typeMShow:false,
				cellphone:"",//
				userName:"",//
				password:"",//
				IDCardNum:"",//
				vicheNo:"",//
				vehicleType:"",//
				vehicleType_code:"",//
				vehicleLength:"",//
				vehicleWide:"",//
				vehicleHeight:"",//
				maxLoadWeight:'',//
				maxLoadVolumn:"",//
				tips_Msg:"",//
				ifTips:false,//
				typeOfCarList:[],//
				dialogImageUrl: '',
		        dialogVisible: false,//
		        uploadParams:{},// 

			}
		},
		mounted(){

			this.$refs['userName'].focus();
		},
		created(){

			var that = this;

			this.cellphone = this.$route.query.phoneNum;

			// 
			this.uploadParams.cellphone = this.cellphone;//

		    if (this.$store.state.typeOfCarList.length) {

		        this.typeOfCarList = that.$store.state.typeOfCarList;

		    } else {

		        var requestData = { 
			        	listName: "TMS-",
			        	tenantCode: "SFKC",
			        };

	            this.httpRequest( "queryCodeLukupData.do",requestData,function(res){

	            	that.typeOfCarList = res.data;
				    that.$store.state.typeOfCarList = res.data;
	            })
		    }
		},
		methods:{
			goPrev(){
				this.$router.push({
					name:"Identifying",
					query:{
						page:"Reg"
					}
				})
			},
			 //   
			regBtn (){

			    var that = this;

			    if(!(that.userName && that.password  && that.IDCardNum && that.vicheNo && that.maxLoadWeight && that.maxLoadVolumn)){

			    	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return ;

			    }else if(!that.vehicleType_code){

			    	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return ;
			    }else if (that.password.length<6){

			      	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return;

			    }else{

			    	// 
			    	this.$refs.upload_info1.submit();
					this.$refs.upload_info2.submit();
					this.$refs.upload_info3.submit();
					this.$refs.upload_info4.submit();

			      	var params = {
			          loginname: "",//
			          username: that.userName,//
			          idNo: that.IDCardNum,// 
			          password: that.password,//
			          cellphone: that.cellphone,//
			          vicheNo: that.vicheNo,//
			          vehicleType: that.vehicleType_code,//
			          vehicleLength: that.vehicleLength,//
			          vehicleWide: that.vehicleWide,//
			          vehicleHeight: that.vehicleHeight,//
			          maxLoadWeight: that.maxLoadWeight,//
			          maxLoadVolumn: that.maxLoadVolumn,//
			        };

			        that.httpRequest( "registerAppUser.do",params,function(res){

			        	that.ifTips = true;

						that.tips_Msg = res.Msg;

						setTimeout(function(){

							that.ifTips = false;
							
							that.toLogin();
						},1500)
			        })
			    }
			},
			// 
			handlePictureCardPreview (file) {

			    this.dialogImageUrl = file.url;
			    this.dialogVisible = true;
			},
			//  
			TypeOfCar (){

			    this.typeMShow = true;
			},
			//    
			SelectComit:function(index){
				this.typeMShow = false;
			    this.vehicleType = this.typeOfCarList[index].description;
			    this.vehicleType_code = this.typeOfCarList[index].code;
			},
			 beforeAvatarUpload(file) {
		        const isLt2M = file.size / 1024 / 1024 < 10;
		        if (!isLt2M) {
		          this.$message.error(' 10MB!');
		        }
		        return isLt2M;
		    },
		}
	}
</script>
<style lang="less" scoped>
	.Reg{
		overflow: hidden;
		.regContainer{
			&>.regInfo{
				&>div{
					position: relative;
				    width:100%;
				    padding: 20/50rem 30/50rem;
				    box-sizing: border-box;
				    overflow: hidden;
				    font-size: 28/50rem;
				    border-bottom: 1/50rem solid #EDEDED;
				    &>span{
					    float: left;
					    width: 170/50rem;
					    height: 50/50rem;
					    line-height: 50/50rem;
					}
					&>input{
						width: 500/50rem;
					    float: left;
					    border: none;
					}
					&>div{
					    float: right;
					    color: #7A7A7A;
					    line-height: 50/50rem;
					    i{
					    	margin-left: 10/50rem;
					    }
					}
				}
				&>p{
					width: 100%;
					height: 80/50rem;
					line-height: 80/50rem;
					background-color: #f0f0f0;
					span{
						float: left;
						margin-left: 30/50rem;
					}
					i{
						font-size: 50/50rem;
						float: right;
						margin-right: 30/50rem;
					}
				}
				&>div.uploadImg{
					div{
						float: none;
					}
					span{
						float: none;
						color: #aaa;
					}
				}
			}
			.g_signBtn{
				margin-bottom: 40/50rem;
			}
			&>button{
			    width:690/50rem;
			    color: #fff;
			    background-color: #ff681d;
			    font-size: 28/50rem;
			    margin-top: 50/50rem;
			}
		}
	}
	@media screen and (max-width: 374px){

	  	.Reg{
			.regContainer{
				&>.regInfo{
					&>div{
					    border-bottom: 3/50rem solid #EDEDED;
					}
				}
			}
		}
	}
</style>


