<template>
	<div  class="ExceptionList">
		<header><i class="iconfont icon-xiangzuo1"   @click="goprev"></i><span></span><i class="iconfont icon-sousuoxiawu-"   @click="ScanQueryOrderInfo"></i></header>
		<div class="container">
			<div class="AbnormalContainer " style="overflow:scroll;">
				<mt-loadmore  topDropText=""   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >

				  <div class="AbnormalList">

					<div class="AbnormalItem" v-for='(dataItem,index) in AbnormalListData'  :id="index"  :key='index'>
						<div class="nomalList">
							<div>
								<span></span>
					    		<span v-clipboard:copy="dataItem.omsNo"
							          v-clipboard:success="onCopy"
							          v-clipboard:error="onError">{{dataItem.omsNo}}
							      <i  v-clipboard:copy="dataItem.omsNo"
							        v-clipboard:success="onCopy"
							        v-clipboard:error="onError"
							        class="iconfont icon-fuzhi" ></i>
							    </span>
							</div>
							<div>
								<span>SF</span>
					    		<span v-clipboard:copy="dataItem.clientOrderNo"
							          v-clipboard:success="onCopy"
							          v-clipboard:error="onError">{{dataItem.clientOrderNo}}
							      <i v-if='dataItem.clientOrderNo' v-clipboard:copy="dataItem.clientOrderNo"
							        v-clipboard:success="onCopy"
							        v-clipboard:error="onError"
							        class="iconfont icon-fuzhi" ></i>
							    </span>
							</div>
							<div><span></span><span>{{dataItem.sponsorTime}}</span></div>
						</div>
						<div class="AreaBox">
						<!-- 	<div>
								<div><span></span><span>{{dataItem.receivePartyCity}}</span></div>
								<div><span></span><span>{{dataItem.receicePartyDistricict}}</span></div>
							</div> -->
							<div><span></span><span>{{dataItem.receivePartyContactName}}</span>&nbsp;&nbsp;&nbsp;&nbsp;<span>{{dataItem.receivePartyPhone}}</span>&nbsp;&nbsp;<i @click="callPhone(dataItem.receivePartyPhone)" class="iconfont icon-dianhua-copy"></i></div>
							<div><span></span><span>{{dataItem.receivePartyAddr1}}</span></div>
						</div>
						<div class="nomalList">
							<div>
								<div><span></span><span>{{dataItem.sponsorName}}</span></div>
								<div><span></span><span>{{dataItem.operateRdcName}}</span></div>
							</div>
						<!-- 	<div>
								<div><span></span><span>{{dataItem.downPerson}}</span></div>
								<div><span></span><span>{{dataItem.downPersonType}}</span></div>
							</div> -->
						<!-- 	<div>
								<div><span></span><span>{{dataItem.transporterName}}</span></div>
								<div><span></span><span>{{dataItem.deliveryMode}}</span></div>
							</div> -->
							<div>
								<div><span></span><span>{{dataItem.abnormalLink}}</span></div>
								<div><span></span><span>{{dataItem.abnormalType}}</span></div>
							</div>
							<!-- <div><span></span><span>{{dataItem.abnormalReason}}</span></div> -->
							<div><span></span><span>{{dataItem.responsibleParty}}</span></div>
							<div><span></span><span>{{dataItem.abnormalExplain}}</span></div>
							<!-- <div><span></span><span>{{dataItem.downResult}}</span></div> -->
						</div>
						<div class="KeyInfoGroup">
							<div><span></span></div>
							<div><span></span></div>
						</div>
						<div class="operateBtn">
							<div @click="abnormalSign(index)"></div>
						</div>
					</div>
					<div v-if="allLoaded && AbnormalListData.length " class="nodata_label"></div>
					<div class="kong"></div>
					<div v-if="is_NoData" class="nodata_kong"></div>
				  </div>
				</mt-loadmore>
			</div>
		</div>
		<!--  -->
		<div   v-if="is_NoData"   class="NoData" >
		  <div>
			<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
			<div>{{is_NoData_text}}</div>
		  </div>
		</div>

		<div v-if="ifTips" class="msg_tips msg_tips1">
			<div class="tips_content">
				<i class="iconfont icon-chenggong1"></i>
				<span>{{tips_Msg}}</span>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default {
		name:"ExceptionList",
		data(){
			return{
				AbnormalListData:[],//
		        is_NoData:false,//  
		        is_NoData_text:"",//  
				numberPerPage: 15,//
				loadPage: 1 ,//
				allLoaded:true,//
				autoFill:false,//
				tips_Msg:"",//
				ifTips:false,
			}
		},
		mounted() {
		},
		created(){
			this.loadData();
		},
		methods:{
			// 
			loadData(load){

				var that = this;

				var postData = {
					currentPage: that.loadPage,//
				    numberPerPage: that.numberPerPage,//
				    cellphone: that.$store.state.userInfo.cellphone,//   15013418360
			    };
			    console.log(postData)

				this.httpRequest("abnormalOrderSFData.do",postData,function(res){

					if(load){
						if(load == "TopLoad"){

							that.$refs.loadmore.onTopLoaded();

						}else if(load == "BottomLoad"){

							that.$refs.loadmore.onBottomLoaded();
						}
					}

					// 
			        if (that.loadPage > 1) {
				   
				        that.AbnormalListData = that.AbnormalListData.concat(res.data)
				      
			        } else {
				       
				        that.AbnormalListData = res.data;

						if(res.data.length){

							that.is_NoData = false; // 

						}else{

							that.is_NoData_text = ""
							

							that.is_NoData = true;
						}
			        }

				    // 
				    if (res.data.length < that.numberPerPage) {
				 
				        that.allLoaded = true;
			
				    } else {
				        
				        that.allLoaded = false;
				      
				    }
				})
			},
			//  
			abnormalSign(index){

				event.stopPropagation();
		
				var that = this;

				this.$router.push({
					name:"AbnormalScan",
					query:{
						omsNo : that.AbnormalListData[index].omsNo,
					}
				})
			},
			//  
			ScanQueryOrderInfo(){

				this.$router.push({
					name:"ScanQueryOrderInfo",
				})
			},
			// 
			goprev(){

				this.$router.push({
					name:"Index",
				})
			},
			// 
			loadTop() {

		        this.loadPage = 1;

		        this.allLoaded = false;

				this.loadData("TopLoad");
			},
			// 
			loadBottom(){

			    var that = this;

			    if (that.allLoaded){
			     	that.$refs.loadmore.onBottomLoaded();
			        return;
			    }else{
			     
			        that.loadPage += 1;
			    }

				this.loadData("BottomLoad");
			},
			onCopy (){

				var that = this;
				that.ifTips = true;
				that.tips_Msg = "";

				setTimeout(function(){

					that.ifTips = false;
				},1000)
			},
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .ExceptionList{
	overflow: hidden;
	font-size: 29/50rem;
	height: 100%;
	background-color:#F4F4F4;
	.container{
	  height: 100%;
	  padding:0 20/50rem 0;
	  background-color:#F4F4F4;
	  .AbnormalContainer{
		  height: 100%;
	  }
	}
	.AbnormalList{
		padding-top: 20/50rem;
		.AbnormalItem{
			position:relative;
		  	background-color:#fff;
			border-radius: 20/50rem;
			text-align: left;
			margin-bottom: 15/50rem;
			overflow: hidden;
			padding: 20/50rem;
			box-sizing:border-box;
			.nomalList{
				&>div{
					line-height: 55/50rem;
					overflow: hidden;
					&>div{
						float: left;
						width: 50%;
					}
				}
				
			}
			.AreaBox{
				border: 1/50rem solid #09D2B7;
				// border: 1/50rem solid @PublicColor;
				border-radius:8/50rem;
				padding:10/50rem;
				margin: 15/50rem 0;
				&>div{
					line-height: 55/50rem;
					overflow: hidden;
					&>div{
						float: left;
						width: 50%;
					}
				}
			}
			.KeyInfoGroup{
				position: absolute;
				top: 20/50rem;
				right: 0;
				display: flex;
				&>div{
					width: auto;
					padding:5/50rem 10/50rem;
					border-radius:10/50rem;
					color: @PublicColor;
					border:1/50rem solid @PublicColor;
					margin-right: 20/50rem;
				}
			}
			.operateBtn{	
				margin-top:20/50rem;
				display: flex;
				justify-content :  flex-end;
				div{
					flex-grow:1;
					max-width: 32%;
					margin-left: 15/50rem;
					text-align: center;
					background-color: @PublicColor;
					color: #fff;
					border-radius: 15/50rem;
					line-height: 60/50rem;
				}
				div:nth-child(1){
					margin-left:0;
				}
			}
		}
	}
	
  }
</style>


