<template>
	<div class="ReportForms">
		<header><span></span></header>
		<div class="container">
			<div v-if="scanIF" class="scan">
				<input>
				<span @click="sys_click"></span>
			</div>
		</div>
		<!--  -->
		<div    class="NoData" >
			<div>
				<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
				<div>{{is_NoData_text}}</div>
			</div>

		</div>
		<FooterIndex/>
	</div>
</template>
<script type="text/javascript">
import FooterIndex from '../../components/footer.vue'

import wx from 'weixin-js-sdk'

	export default{
		name:"ReportForms",
		data(){
			return{
				is_NoData_text:'',
				scanIF:false
			}
		},
		components:{
		  	FooterIndex
		},
		methods:{
			sys_click(){
	        //url
	        $.get("?url=", function(data){
	        var jsondata=$.parseJSON(data);
	            wx.config({
	                // ,apialertpclogpc
	                debug: true,
	                // 
	                appId: jsondata.model.appId,
	                // 
	                timestamp: "" + jsondata.model.timestamp,
	                // 
	                nonceStr: jsondata.model.nonceStr,
	                // 
	                signature: jsondata.model.signature,
	                // JSJS
	                jsApiList: ['checkJsApi', 'scanQRCode']
	            });
	        });
	        wx.error(function (res) {
	            alert("" + res.errMsg);//wx.config
	        });
	 
	        wx.ready(function () {
	            wx.checkJsApi({
	                jsApiList: ['scanQRCode'],
	                success: function (res) {
	 
	                }
	            });
	 
	                wx.scanQRCode({
						needResult: 1, // 01
						scanType: ["qrCode","barCode"], // 
						success: function (res) {
							var result = res.resultStr; // needResult  1 
						}
					});
	 
	        }); 
	        }

		}
	}
</script>
<style lang="less" scoped>
	.ReportForms{
		overflow: hidden;
		.scan{
			margin-top: 20/50rem;
			line-height: 60/50rem;
			input{
				margin:0 20/50rem;
				float: left;
				height: 60/50rem;
			}
			span{
				background-color: #5965D8;
				color: #fff;
				padding: 0 20/50rem;
				border-radius: 8/50rem;
				float: left;
			}
		}
	}
</style>


