<template>
	<div class="PersonalData">
		<header><i class="iconfont icon-xiangzuo1"  @click="goPrev"></i><span></span></header>
		<div class='container'>
		  <div class='regInfo'>
		  	<p class="mine"><img src="../../assets/images/mine.png"><span></span></p>
		    <div><span></span><input  readonly  v-model='userName'></input></div>
		    <div><span></span><input   readonly v-model='cellphone'></input></div>
		    <div><span></span><input placeholder=''   ref="IDCardNum"   @keyup.enter="getFocus('vicheNo')"    v-model='IDCardNum'></input><i v-if="IDCardNum" @click="clearInput('IDCardNum')" class="iconfont icon-iconfontcuowu"></i></div>

		    <p  class="car"><img src="../../assets/images/car.png"><span></span></p>
		    <div><span></span><input   placeholder='' ref="vicheNo" v-model='vicheNo'></input><i v-if="vicheNo" @click="clearInput('vicheNo')" class="iconfont icon-iconfontcuowu"></i></div>
		    <div @click='TypeOfCar'><span></span><div ><span>{{vehicleType}}</span><i class="iconfont icon-xiangshang"></i></div></div>
		    <div><span></span><input  placeholder='' type="number"   ref="vehicleLength" v-model='vehicleLength'></input><i v-if="vehicleLength" @click="clearInput('vehicleLength')" class="iconfont icon-iconfontcuowu"></i></div>
		    <div><span></span><input   placeholder='' type="number"   ref="vehicleWide" v-model='vehicleWide'></input><i v-if="vehicleWide" @click="clearInput('vehicleWide')" class="iconfont icon-iconfontcuowu"></i></div>
		    <div><span></span><input  placeholder='' type="number"   ref="vehicleHeight" v-model='vehicleHeight'></input><i v-if="vehicleHeight" @click="clearInput('vehicleHeight')" class="iconfont icon-iconfontcuowu"></i></div>
		    <div><span></span><input placeholder=''   ref="maxLoadWeight"   @keyup.enter="getFocus('maxLoadVolumn')"  type="number"   v-model='maxLoadWeight'></input><i v-if="maxLoadWeight" @click="clearInput('maxLoadWeight')" class="iconfont icon-iconfontcuowu"></i></div>
		    <div><span></span><input placeholder='' type="number"    ref="maxLoadVolumn"   @keyup.enter="regBtn"    v-model='maxLoadVolumn'></input><i v-if="maxLoadVolumn" @click="clearInput('maxLoadVolumn')" class="iconfont icon-iconfontcuowu"></i></div>

		    <p  class="upload"><img src="../../assets/images/upload.png"><span></span></p>
			<div class="uploadImg">
				<div class="LoadImg_head"></div>
				<div>
					<el-upload :action = "HOST+'registerUploadPicture.do'"  :limit="2" :multiple="true"  :data="uploadParams" ref='upload_INFO1' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</div>
			</div>
			<div class="uploadImg">
				<div class="LoadImg_head"></div>
				<div>
					<el-upload  :action= "HOST + 'registerUploadPicture.do'"  :limit="2" :multiple="true"  :data="uploadParams" ref='upload_INFO2' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</div>
			</div>
			<div class="uploadImg">
				<div class="LoadImg_head"></div>
				<div>
					<el-upload  :action= "HOST + 'registerUploadPicture.do'"  :limit="3" :multiple="true"  :data="uploadParams" ref='upload_INFO3' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</div>
			</div>
			<div class="uploadImg">
				<div class="LoadImg_head"></div>
				<div>
					<el-upload  :action= "HOST + 'registerUploadPicture.do'"  :limit="3" :multiple="true"  :data="uploadParams" ref='upload_INFO4' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"   :on-error="uploadError" :on-success="uploadSuccess"   :on-preview="handlePictureCardPreview" >
					  <i class="el-icon-plus"></i>
					</el-upload>
					<el-dialog :visible.sync="dialogVisible">
					  <img width="100%" :src="dialogImageUrl" alt="">
					</el-dialog>
				</div>
			</div>
		  </div>
		  <div class="g_signBtn" @click='saveInfo'></div>
		</div>

		<!-- -->
		<div class="SelectMeng"   v-if="typeMShow">
			<div class="SelectWarehouses" >
				<div class="SelectTitle"></div>
				<div class="SelectItem">
					<div @click="SelectComit(index)"  v-for='(dataItem,index) in typeOfCarList'  :id="index"  :key='index'>{{dataItem.description}}</div>
				</div>
				<div class="cancel"   @click="selectCancel('e')"></div>
			</div>
		</div>
		<div v-if="ifTips" class="msg_tips">
			<div class="tips_content">
				<i class="iconfont icon-chenggong1"></i>
				<p>{{tips_Msg}}</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"PersonalData",
		data(){
			return{
			    typeMShow:false,// 
			    typeOfCarList:[], // 
			    userName:"",//
			    cellphone:"",//
			    IDCardNum:'',//
			    vicheNo:"",//
			    vehicleType:"",//
			    vehicleType_code:"",//
			    vehicleLength:"",//
			    vehicleWide:"",//
			    vehicleHeight:"",//
			    maxLoadWeight:"",//
			    maxLoadVolumn:"",//
			    tips_Msg:"",//
				ifTips:false,
				dialogImageUrl: '',
		        dialogVisible: false,//
		        uploadParams:{},// 
			}
		},
		created(){

			var that = this;

			var userInfo = this.$store.state.userInfo;

			// 
			this.uploadParams.cellphone = userInfo.cellphone;//

			var getUserData = { cellphone: userInfo.cellphone };

            this.httpRequest( "getUserInfo.do",getUserData,function(resData){
            	console.log(resData)

            	that.$store.state.userInfo = resData.data;

            	that.userName = resData.data.userName;//
				that.cellphone = resData.data.cellphone;//
				that.IDCardNum = resData.data.idNo;// 
				that.vicheNo = resData.data.vicheNo;//
				that.vehicleType_code = resData.data.vehicleType;//
				that.vehicleLength = resData.data.vehicleLength;//
				that.vehicleWide = resData.data.vehicleWide;//
				that.vehicleHeight = resData.data.vehicleHeight;//
				that.maxLoadWeight = resData.data.maxLoadWeight;//
				that.maxLoadVolumn = resData.data.maxLoadVolumn;//

				if (that.$store.state.typeOfCarList.length) {

			        that.typeOfCarList = that.$store.state.typeOfCarList;

			        for(var i = 0;i < that.typeOfCarList.length; i++){

			        	if(that.typeOfCarList[i].code == that.vehicleType_code){

			        		that.vehicleType = that.typeOfCarList[i].description;
			        	}
			        }

			    } else {

			        var requestData = { 
			        	listName: "TMS-",
			        	tenantCode: "SFKC",
			        };

		            that.httpRequest( "queryCodeLukupData.do",requestData,function(res){

		            	that.typeOfCarList = res.data;
					    that.$store.state.typeOfCarList = res.data;

					    for(var i = 0;i < that.typeOfCarList.length; i++){

				        	if(that.typeOfCarList[i].code == that.vehicleType_code){

				        		that.vehicleType = that.typeOfCarList[i].description;
				        	}
				        }
		            })
			    }
            })
		},
		methods:{
			goPrev(){
				var that = this;

				this.$router.push({
					name:"HomeIndex",
					query:{
					}
				})
			},
			//  
			saveInfo(){
				
				var that = this;

			    if (!(that.IDCardNum && that.vicheNo && that.maxLoadWeight && that.maxLoadVolumn)){
			    	
			    	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return ;

			    }else if(!that.vehicleType_code){

			    	that.$alert("", '', {
				        confirmButtonText: '',
				   	})
				   	return ;

			    }else{

			    	var userInfo = that.$store.state.userInfo;

			    	// 
			    	this.$refs.upload_INFO1.submit();
					this.$refs.upload_INFO2.submit();
					this.$refs.upload_INFO3.submit();
					this.$refs.upload_INFO4.submit();
			     
				    var params = {
				        loginName: userInfo.loginName,
				        userName: userInfo.userName,
				        user_id: userInfo.user_id,
				        password: "",
				        cellphone: userInfo.cellphone,
				        idNo: that.IDCardNum,// 
				        vicheNo: that.vicheNo,
				        vehicleType: that.vehicleType_code,
				        vehicleLength: that.vehicleLength,
				        vehicleWide: that.vehicleWide,
				        vehicleHeight: that.vehicleHeight,
				        maxLoadWeight: that.maxLoadWeight,//
					    maxLoadVolumn: that.maxLoadVolumn,//
				    };

				    that.httpRequest( "regAppUserUpdate.do",params,function(res){

					    that.ifTips = true;
						that.tips_Msg = "";

						setTimeout(function(){

							that.ifTips = false;

							that.$router.push({
								name:"HomeIndex",
								query:{
								}
							})
						},1500)
		            })
			    } 
			},
			//   
			TypeOfCar(){
				this.typeMShow = true;
			},
			//    
			SelectComit:function(index){
				this.typeMShow = false;
			    this.vehicleType = this.typeOfCarList[index].description;
			    this.vehicleType_code = this.typeOfCarList[index].code;
			},
			// 
			handlePictureCardPreview (file) {

			    this.dialogImageUrl = file.url;
			    this.dialogVisible = true;
			},
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
	.PersonalData{overflow: hidden;
		.container{
		  	font-size: 26/50rem;
		  	&>.regInfo{
		  		&>div{
		  			position: relative;
				  	padding: 20/50rem 30/50rem;
				  	box-sizing: border-box;
				  	overflow: hidden;
				  	font-size: 28/50rem;
				  	border-bottom: 1/50rem solid #EDEDED;
				  	input{
				  		width: 500/50rem;
					    float: left;
				  		border:none;
				  	}
				  	&>span{
					  	float: left;
					  	width: 140/50rem;
					  	height: 50/50rem;
					  	line-height: 50/50rem;
					}
					&>div{
					  	line-height: 50/50rem;
					  	float: right;
					  	color: #7A7A7A;
					  	i{
					  		margin-left: 10/50rem;
					  	}
					}
				}
				&>p{
					width: 100%;
					height: 80/50rem;
					line-height: 80/50rem;
					border-top: 10/50rem solid #f0f0f0;
					border-bottom: 5/50rem solid #f0f0f0;
					img{
						float:left;
						margin:7.5/50rem  20/50rem;
						height: 65/50rem;
					}
					span{
						float: left;
						font-weight:700;
						font-size:30/50rem;
						line-height: 80/50rem;
					}
				}
				&>p.mine{
					border-top:none;
				}
				&>div.uploadImg{
					div{
						float: none;
					}
					span{
						float: none;
						color: #aaa;
					}
				}
		  	}
		  	.g_signBtn{
		  		margin-bottom: 40/50rem;
		  		margin-top: 50/50rem;
		  	}
		}
	}
	@media screen and (max-width: 374px){
		.PersonalData{
			.container{
			  	&>.regInfo{
			  		&>div{
					  	border-bottom: 3/50rem solid #EDEDED;
					}
			  	}
			}
		}
	}
</style>


