<template>
  <div class="commit-footer">
    <div class="left-count">
      <div class="top">
        <!-- :<i>{{ orderCount }}</i> -->
      </div>
      <div class="bottom">
        :<i>{{ count.length }}</i>
      </div>
    </div>
    <div></div>
    <div v-if='!hideBitch' class="commit-button BatchCommit" @click='BatchCommit'></div>
    <div class="commit-button" @click="checkForCommit"></div>
    <v-confirm ref="myConfirm" @userBehavior="userBehaviorFun"></v-confirm>

    <el-dialog title=""
               class="img-dialog"
               :visible.sync="commitImgDialog"
               :close-on-click-modal="false"
               :show-close="false"
               append-to-body width="90%">
      <el-upload :action="HOST + 'scanLabel.do'"
                 ref="upload"
                 class="avatar-uploader"
                 :data="params"
                 name="fileupload"
                 :multiple="false"
                 :show-file-list="false"
                 :on-success="handleAvatarSuccess"
                 :before-upload="beforeAvatarUpload"
                 :on-change="getTheFile"
                 :auto-upload="true">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeImgInput"> </el-button>
        <!--<el-button type="primary" @click="commitImg"> </el-button>-->
      </span>
    </el-dialog>
    <!--  -->
    <div class="BatchCommitPopBox" v-show='BatchCommit_show'>
      <div class="BatchCommit_">
        <div class="BatchCommit_head"></div>
        <div><span></span><span>{{actualQty}}</span></div>
        <div class="BatchCommit_content">
          <input type="number" v-model='BatchCommitNumber'>
        </div>
        <div class="BatchCommit_btn">
          <div class="commit-button" @click='BatchCommitCancel'></div>
          <div class="commit-button" @click='BatchCommitConfirm'></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import { addOrderList } from "../api/api";
  import Confirm from '../components/confirm'
  import { bus } from '../utils/bus'
  import Axios from "axios"
  let location = null
  export default {
    name: "commit-footer",
    components: {
      'v-confirm': Confirm
    },
    data() {
      return {
        params: {
          params: [],
          longitude: '', // 
          latitude: '', // 
        },
        CurrentCity:'',//
        CurrentLocation:'',//
        cleanOrderList: [],
        commitImgDialog: false,
        imageUrl: null,
        imgObj: null,
        BatchCommit_show:false,// 
        BatchCommitNumber:'',//
      }
    },
    props: [
      'count', // 
      'orderCount', // 
      'countData',
      'orderList',
      'cacheResponseData',
      'deliveryId',
      'hideBitch',
      'transportLinks',//
      'actualQty',//
    ],
    mounted() {
      this.getTheLocation() // APP

    },
    methods: {
      // 
      BatchCommit(){

        if(this.count.length === 0) {

          return;
        }
        this.BatchCommit_show = true;

        this.$emit('scanFun', false) // 
      },
      //  
      BatchCommitCancel(){
        this.BatchCommit_show = false;
        this.$emit('scanFun', true) // 
      },
      BatchCommitConfirmitem(){
        var that = this;


        var path = this.$route.path
        var linkNode = '' // linkNode

        switch (path) {
          case '/DeliveryScanXH':
            linkNode = '10'
            break
          case '/DeliveryScan':
            linkNode = '10'
            break
          case '/TransitScan':
            linkNode = '20'
            break
          case '/DeliveryOrderPickScan':
            linkNode = '30'
            break
          case '/ReceiptScan':
            linkNode = '40'
            break
          default:
            linkNode = '10'
            break
        }

        var postData = {
          deliveryId: that.deliveryId,//id
          labelNumber: that.BatchCommitNumber,//
          transportLinks:linkNode,//
        };

        console.log(postData)

        Axios.post(that.HOST+"batchScanLabel.do",{"params": JSON.stringify(postData)})
        .then(function(res){
          console.log(res)
          
          if(res.data.status=='1'){

            that.$alert('', '', {
              confirmButtonText: '',
              callback(){
                that.$router.go(-1)
              }
            })

          }else{

            var Msg;
            if(res.data.Msg){

              Msg = res.data.Msg;
            }else {

              Msg = "";
            }
            that.$emit('reflash', true);
            that.$emit('scanFun', true);
            alert(Msg);
          }
        })
        .catch(function(res){

          that.$emit('reflash', true);
          that.$emit('scanFun', true);
          alert("");
        })
      },
      //  
      BatchCommitConfirm(){
        var that = this;
        if(!this.BatchCommitNumber){
          return;
        }

        const _this = this
        let path = this.$route.path
        let linkNode = '' // linkNode
        const scanPerson = this.$store.state.userInfo.userName //  *
        const supplierId = this.$store.state.userInfo.orgnizationId // ID
        const supplierName = this.$store.state.userInfo.orgnizationName // 
        const longitude = this.longitude
        const latitude = this.latitude
        const address = this.CurrentLocation;
        const city = this.CurrentCity;
        switch (path) {
          case '/DeliveryScanXH':
            linkNode = '10'
            break
          case '/DeliveryScan':
            linkNode = '10'
            break
          case '/TransitScan':
            linkNode = '20'
            break
          case '/DeliveryOrderPickScan':
            linkNode = '30'
            break
          case '/ReceiptScan':
            linkNode = '40'
            break
          default:
            linkNode = '10'
            break
        }
        let params = []
        for(let key in this.count) {
          let obj = {}
          obj.labelNo = this.count[key].order
          obj.linkNode = linkNode
          obj.scanPerson = scanPerson
          obj.scanTime = this.count[key].time
          obj.supplierId = supplierId
          obj.supplierName = supplierName
          obj.longitude = longitude
          obj.latitude = latitude
          obj.address = address
          obj.city = city

          params.push(obj)
        }

        let displayWtList = []
        for(let i in this.cacheResponseData) {
          let cacheObj = {}
          let item = this.cacheResponseData[i]
          cacheObj.omsNo = item.omsNo
          cacheObj.displayWt = item.displayWt
          cacheObj.linkNode = linkNode

          displayWtList.push(cacheObj)
        }

        this.cleanOrderList = []
        for (let item in this.count) {
          this.cleanOrderList.push(this.count[item].order)
        }
        if(linkNode === '10') {

          if(this.$route.path == '/DeliveryScanXH'){
            
            this.params = {
              "params" : JSON.stringify(params),
              "labelNo": JSON.stringify(this.cleanOrderList),
              "deliveryId":that.deliveryId
            }
            console.log(this.params)
            addOrderList(this.params).then(res => {
              console.log(res)
              if(res.data.status == '1') {
                console.log(res)
                // alert(res.data.Msg)
                that.BatchCommitConfirmitem();
                
              }else {
                console.log(res)
                alert(res.data.Msg)
                this.$emit('scanFun', true)
              }
            })

          }else{
            function getLink10Data() {
              return new Promise(resolve => {
                _this.params = { // 
                  "params" : JSON.stringify(params),
                  "labelNo": JSON.stringify(_this.cleanOrderList),
                  "omsNo_displayWt": JSON.stringify(displayWtList),
                  "deliveryId":that.deliveryId
                }
                resolve(_this.params)
              })
            }
            getLink10Data()
          }
        } else if(linkNode === '40') {
          function getLink40Data() {
            return new Promise(resolve => {
              _this.params = {
                "params" : JSON.stringify(params),
                "labelNo": JSON.stringify(_this.cleanOrderList),
                "deliveryId":that.deliveryId
              }
              resolve(_this.params)
            })
          }

          getLink40Data()
          // .then(data => {
          //   this.$refs.upload.submit()
          // })
        } else {
          this.params = {
            "params" : JSON.stringify(params),
            "labelNo": JSON.stringify(this.cleanOrderList),
            "deliveryId":that.deliveryId
          }
          addOrderList(this.params).then(res => {
              console.log(res)
            if(res.data.status == '1') {
              console.log(res)
              // alert(res.data.Msg)
              // this.$emit('reflash', true)
              // this.$emit('scanFun', true)

              that.BatchCommitConfirmitem();

            }else {
              console.log(res)
              alert(res.data.Msg)
              this.$emit('scanFun', true)
            }
          })
        }
      },
      getTheFile(file) {
        console.log(file)
        console.log(this.params)
        this.imageUrl = file.url
      },
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 10;
        if (!isLt2M) {
          this.$message.error(' 10MB!');
        }
        return isLt2M;
      },
      handleAvatarSuccess(res, file) {
        console.log(res)
        this.imageUrl = URL.createObjectURL(file.raw);
        if (res.status === '1') {
          console.log(res)
          alert(res.Msg)
          this.commitImgDialog = false
          this.imageUrl = ''
          this.params = {}
          this.$refs.upload.clearFiles()
          this.$emit('reflash', true)

          // bus.$emit('switchScan', true)
          this.$emit('scanFun', true) // 
        } else {
          alert(res.Msg)
        }
      },
      closeImgInput() {
        this.commitImgDialog = false
        // bus.$emit('switchScan', true) // 
        this.$emit('scanFun', true) // 
      },
      commitImg() {
        this.commitImgDialog = false
        this.$refs.myConfirm.show('', {
          type: 'confirm',
          cancelText: '',
          confirmText: '',
          titleText: '',
          data: this.count
        })
      },
      selectFile(item) {
        this.imgUrl = item
        this.setTheFormData()
      },
      setTheFormData() {
        const _this = this
        let formData = new FormData()
        formData.append('data', this.imgUrl.srcElement.files[0])
        this.imgObj = formData.get('data')

        let reader = new FileReader()
        reader.readAsDataURL(formData.get('data'))
        reader.onload = function(e) {
          _this.$refs.img.src = e.target.result
        }
      },
      /**
       * 
       **/
      getTheLocation() {
        var that = this
        if (!window.plus) return;

        location = new plus.geolocation.getCurrentPosition(p => {

          that.longitude = p.coords.longitude
          that.latitude = p.coords.latitude

          if(p.coords.longitude && p.coords.latitude){

            AMap.plugin('AMap.Geocoder', function() {

              var geocoder = new AMap.Geocoder({
                // city adcode  citycode
                city: '010'
              })

              var lnglat = [p.coords.longitude, p.coords.latitude];

              geocoder.getAddress(lnglat, function(status, result) {

                console.log(status)
                console.log(result)

                if (status === 'complete' && result.info === 'OK') {
                  // result
                  that.CurrentCity = result.regeocode.addressComponent.city?result.regeocode.addressComponent.city:result.regeocode.addressComponent.province;
                  that.CurrentLocation = result.regeocode.formattedAddress;

                }else{

                  alert('');
                }
              })
            })

          }else{
            alert('');
          }
        }, err => {
          console.log(err)
          alert(err.message)
        })
      },
      userBehaviorFun(type, data) {
        if(type !== 'clickCancel') {
          this.commitData()
        }else {
          this.$emit('scanFun', true)
        }
      },
      // 
      checkForCommit() {
        if(this.count.length !== 0) {
          // this.getTheLocation()
          if (this.$route.path === '/DeliveryScan' || this.$route.path === '/ReceiptScan') {
            this.commitImgDialog = true
            // bus.$emit('switchScan', false) // 
            this.$emit('scanFun', false) // 
            this.commitData()
          } else {
            this.$refs.myConfirm.show('', {
              type: 'confirm',
              cancelText: '',
              confirmText: '',
              titleText: '',
              data: this.count
            })
            // bus.$emit('switchScan', false) // 
            this.$emit('scanFun', false) // 
          }
        }
      },
      /**
       * 
       */
      commitData() {

        var that = this;
        const _this = this
        let path = this.$route.path
        let linkNode = '' // linkNode
        const scanPerson = this.$store.state.userInfo.userName //  *
        const supplierId = this.$store.state.userInfo.orgnizationId // ID
        const supplierName = this.$store.state.userInfo.orgnizationName // 
        const longitude = this.longitude
        const latitude = this.latitude
        const address = this.CurrentLocation;
        const city = this.CurrentCity;
        switch (path) {
          case '/DeliveryScanXH':
            linkNode = '10'
            break
          case '/DeliveryScan':
            linkNode = '10'
            break
          case '/TransitScan':
            linkNode = '20'
            break
          case '/DeliveryOrderPickScan':
            linkNode = '30'
            break
          case '/ReceiptScan':
            linkNode = '40'
            break
          default:
            linkNode = '10'
            break
        }
        let params = []
        for(let key in this.count) {
          let obj = {}
          obj.labelNo = this.count[key].order
          obj.linkNode = linkNode
          obj.scanPerson = scanPerson
          obj.scanTime = this.count[key].time
          obj.supplierId = supplierId
          obj.supplierName = supplierName
          obj.longitude = longitude
          obj.latitude = latitude
          obj.address = address
          obj.city = city

          params.push(obj)
        }

        let displayWtList = []
        for(let i in this.cacheResponseData) {
          let cacheObj = {}
          let item = this.cacheResponseData[i]
          cacheObj.omsNo = item.omsNo
          cacheObj.displayWt = item.displayWt
          cacheObj.linkNode = linkNode

          displayWtList.push(cacheObj)
        }

        this.cleanOrderList = []
        for (let item in this.count) {
          this.cleanOrderList.push(this.count[item].order)
        }
        if(linkNode === '10') {

          if(this.$route.path == '/DeliveryScanXH'){
            
            this.params = {
              "params" : JSON.stringify(params),
              "labelNo": JSON.stringify(this.cleanOrderList),
              "deliveryId":that.deliveryId
            }
            addOrderList(this.params).then(res => {
              console.log(res)
              if(res.data.status == '1') {
                console.log(res)
                alert(res.data.Msg)

                this.$emit('reflash', true)
                this.$emit('scanFun', true)
              }else {
                console.log(res)
                alert(res.data.Msg)
                this.$emit('scanFun', true)
              }
            })

          }else{
            function getLink10Data() {
              return new Promise(resolve => {
                _this.params = { // 
                  "params" : JSON.stringify(params),
                  "labelNo": JSON.stringify(_this.cleanOrderList),
                  "omsNo_displayWt": JSON.stringify(displayWtList),
                  "deliveryId":that.deliveryId
                }
                resolve(_this.params)
              })
            }
            getLink10Data()
          }
        } else if(linkNode === '40') {
          function getLink40Data() {
            return new Promise(resolve => {
              _this.params = {
                "params" : JSON.stringify(params),
                "labelNo": JSON.stringify(_this.cleanOrderList),
                "deliveryId":that.deliveryId
              }
              resolve(_this.params)
            })
          }

          getLink40Data()
          // .then(data => {
          //   this.$refs.upload.submit()
          // })
        } else {
          this.params = {
            "params" : JSON.stringify(params),
            "labelNo": JSON.stringify(this.cleanOrderList),
            "deliveryId":that.deliveryId
          }
          addOrderList(this.params).then(res => {
              console.log(res)
            if(res.data.status == '1') {
              console.log(res)
              alert(res.data.Msg)
              this.$emit('reflash', true)
              this.$emit('scanFun', true)
            }else {
              console.log(res)
              alert(res.data.Msg)
              this.$emit('scanFun', true)
            }
          })
        }
      }
    }
  }
</script>

<style scoped lang="less">
  @import "publicLess.less";
  .my-confirm {
    
  }
</style>



