<template>
    <div class="Search">
	    <header><i class="iconfont icon-xiangzuo1"  @click="goPrev"></i><span></span></header>
		<div class="container">
  			<div class='CompetitionScreen'>
    			<div class='screen'>
      			    <div><span></span><el-date-picker id="startvalue"  :editable='false' v-model="startvalue"  type="date"  placeholder=""></el-date-picker></div>
      			    <div><span></span><el-date-picker id="endValue"  :editable='false' v-model="endValue"  type="date"  placeholder=""></el-date-picker></div>
                    
                    <div>
                        <span> &nbsp;&nbsp;</span>
                        <input v-model='orderNum' placeholder=''  ></input>
                        <i v-if="orderNum" @click="clearInput('orderNum')" class="iconfont icon-iconfontcuowu"></i>
                    </div>
                    <div>
                        <span></span>
                        <input v-model='order_WaybillNum' placeholder=''  ></input>
                        <i v-if="order_WaybillNum" @click="clearInput('order_WaybillNum')" class="iconfont icon-iconfontcuowu"></i>
                    </div>
                    <div>
                        <span></span>
                        <el-select v-model="TransportLink" placeholder="">
                            <el-option
                              v-for="item in TsptLinkList"
                              :key="item.value"
                              :label="item.label"
                              :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
		        </div>
                <div class="g_doubleBtn">
                    <div @click="reset"></div>
                    <div @click="sureScreen"></div>
                </div>
		    </div>
	    </div>
	</div>
</template>
<script type="text/javascript">
    
    import $ from "jquery"
	export default{
		name:"Search",
		data(){
			return{
                startvalue:null,//
                endValue:null,//

                orderNum:null,//
                order_WaybillNum: null,//
                TsptLinkList: [{
                  value: '',
                  label: ''
                }, {
                  value: '',
                  label: ''
                }, {
                  value: '',
                  label: ''
                }, {
                  value: '',
                  label: ''
                }],
                TransportLink: '',//
			}
		},
        created(){

            var that = this;

            this.startvalue =  that.$store.state.history_SearchData.startDate;
            this.endValue =  that.$store.state.history_SearchData.endDate;
            this.orderNum =  that.$store.state.history_SearchData.orderNoinput;//
            this.order_WaybillNum =  that.$store.state.history_SearchData.shipmentCodeinput;//
            this.TransportLink =  that.$store.state.history_SearchData.TransportLink;//
  
        },
    	methods:{
            goPrev(){
                var that = this;

                // 
                this.$store.state.history_needRefresh = false;

                this.$router.push({
                    name:"historyList",
                    query:{
                    }
                })
            },
    		//   
            sureScreen(){

                var that = this;

                var startDate,endDate;

                if($("#startvalue").val()){

                    startDate = $("#startvalue").val();

                }else{
                    startDate =null;
                }

                if($("#endValue").val()){

                    endDate = $("#endValue").val();

                }else{
                    endDate =null;
                }

                var LinkStatus = this.$store.state.history_LinkStatus;

                that.$store.state.history_SearchData = {
                    startDate: startDate,// 
                    endDate: endDate,//  
                    orderNoinput: that.orderNum,// 
                    shipmentCodeinput: that.order_WaybillNum,//  
                    TransportLink: that.TransportLink,//
                    cellphone: that.$store.state.userInfo.cellphone,//
                    transportLinks:LinkStatus == "Link_PickUp" ? "":(LinkStatus == "Link_Transit" ? "":(LinkStatus == "Link_ToSend" ? "":""))
                }

                console.log(that.$store.state.history_SearchData)

                that.$store.state.history_NoSearchFrom = false;// false

                that.$router.push({
                    name:"historyList",
                    query:{
                       
                    }
                })
            },
            // 
            reset(){
                this.startvalue = null;//
                this.endValue = null;//

                // 
                this.orderNum = null;//
                this.order_WaybillNum =  null;//
                this.TransportLink = null;//
            },
    	}	
    }
</script>
<style lang="less" scoped>
.Search{
    overflow: hidden;
}
  	.container{
        overflow: hidden;
    	.CompetitionScreen{
            height: 100%;
            font-size: 28/50rem;
            &>.screen{
                background-color: #fff;
                &>div{
                    border-bottom: 1/50rem solid #EDEDED;
                    padding:0 20/50rem;
                    box-sizing: border-box;
                    position: relative;
                    height: 95/50rem;
                    line-height: 95/50rem;
                    &>span{
                        width: 150/50rem;
                        float: left;
                    }
                    &>input{
                        float: left;
                        border: none;
                        width:500/50rem;
                        height: 92/50rem;
                    }
                    .icon-iconfontcuowu{
                        right:50/50rem;
                        top: 4/50rem;
                    }
                }
            }
        }
  	}
    @media screen and (max-width: 374px){

      .container{
        .CompetitionScreen{
            &>.screen{
                &>div{
                  border-bottom: 3/50rem solid #EDEDED;
                }
            }
        }
      }
    }
</style>


