<template>
  <div class="body Abnormal">
    <div class="common-box">
      <header><i class="iconfont icon-xiangzuo1" @click="goPrev"></i><span></span></header>
      <div v-if='!omsNo' class="content-box">
        <v-scan @scanData="scanData" :switchScan="switchScan"></v-scan>
      </div>
    </div>
    <div class="form-content">
      <div class="form-item">
        <label></label>
        <el-input class="form-input" type="text" v-model="OMS.omsNo" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label></label>
        <el-input class="form-input" type="text" v-model="OMS.deliveryNoSF" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label></label>
        <el-input class="form-input" type="text" v-model="$store.state.userInfo.orgnizationName" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label></label>
        <el-input type="text" v-model="$store.state.userInfo.userName" :disabled="true"></el-input>
      </div>
      <div class="form-item">
        <label></label><br>
        <el-select v-model="aboType" placeholder="">
          <el-option
            v-for="item in typeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label></label><br>
        <el-select v-model="aboReason" placeholder="">
          <el-option
            v-for="item in reasonOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label></label><br>
        <el-select v-model="aboLink" placeholder="">
          <el-option
            v-for="item in linkOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label></label><br>
        <el-select v-model="aboPer" placeholder="">
          <el-option
            v-for="item in perOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
      <div class="form-item">
        <label></label>
        <el-input
          type="textarea"
          :rows="2"
          placeholder=""
          v-model="aboText">
        </el-input>
      </div>
    </div>
    <div class="uploadImg" >

      <div><span>(3)</span></div>

      <el-upload  :action = " HOST + 'abnormalUploadImage.do'" 
                :auto-upload="false"  
                :data="uploadParams"  
                list-type="picture-card"  
                ref="upload" 
                :limit="3" 
                :multiple="true"
                :on-error="uploadErr" 
                 :show-file-list="true"
                :on-success="uploadSuccessful"    
                 :before-upload="beforeAvatarUpload"
                :on-preview="handlePicturePreview" >
        <i class="el-icon-plus"></i>
      </el-upload>
    
<!-- 
      <el-upload :action="HOST + 'reportAbnormal.do'"
                 ref="upload"
                 class="avatar-uploader"
                 :data="params"
                 name="fileupload"
                 :multiple="false"
                 :show-file-list="true"
                 :on-success="handleAvatarSuccess"
                 :before-upload="beforeAvatarUpload"
                 :on-change="getTheFile"
                 >
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload> -->
      <el-dialog :visible.sync="dialogVisible">
        <img width="100%" :src="dialogImageUrl" alt="">
      </el-dialog>
    </div>
     
    <div class="footer-commit">
      <div class="commit" @click="commitAbo"></div>
    </div>
<!-- 
    <el-dialog title=""
               class="img-dialog"
               :visible.sync="commitImgDialog"
               :close-on-click-modal="false"
               :show-close="false"
               append-to-body width="90%">
      <span slot="footer" class="dialog-footer">
        <el-button @click="closeImgInput"> </el-button>
        <el-button type="primary" @click.native="commitImg"> </el-button>
      </span>
    </el-dialog> -->
  </div>
</template>

<script>
  import Scan from '@/components/Scan'
  import Header from '@/components/header'
  import { getTheOrderNumber, abnormalDropList, commitAbnormal } from "../../../api/api";
  import { bus } from '../../../utils/bus'
  import Axios from "axios"
  import $ from "jquery"
  export default {
    name: "Abnormal",
    components: {
      'v-scan': Scan,
      'v-header': Header
    },
    data() {
      return {
        headerName: '',
        responseData: {},
        OMS: {},

        aboType: '', //
        typeOptions: [], // 

        aboReason: '',
        reasonOptions: [],

        aboLink: '',
        linkOptions: [],

        aboPer: '',
        perOptions: [],

        aboText: '', // 

        // commitImgDialog: false,
        dialogImageUrl: '',//
        dialogVisible: false,//
        imgUrl: null,
        imgObj: null,
        uploadParams: {},//
        imageUrl: '',
        switchScan: true,
        uploadSuccessNum:0,//
        uploadTotalNum:0,//
        uploadFailNum:0,//
        shipmentCode:'',//
        omsNo:'',//
        deliveryNoSF:'',//SF
      }
    },
    mounted() {
      this.getAboList();
    },
    created(){
      
      var that = this;
      if(this.$route.query.shipmentCode){
        this.shipmentCode = this.$route.query.shipmentCode;
      }
      if(this.$route.query.omsNo){
        this.omsNo = this.$route.query.omsNo;
        
        this.OMS.omsNo = that.omsNo;
      }
      if(this.$route.query.deliveryNoSF){
        this.deliveryNoSF = this.$route.query.deliveryNoSF;
        
        this.OMS.deliveryNoSF = that.deliveryNoSF;
      }else{
        this.OMS.deliveryNoSF = "";
      }
      
    },
    methods: {
      // 
      beforeAvatarUpload(file) {
        const isLt2M = file.size / 1024 / 1024 < 10;
        if (!isLt2M) {
          this.$message.error(' 10MB!');
        }
        return isLt2M;
      },
      // 
      uploadSuccessful  (response, file, fileList){

          var that = this;
          console.log(response)
          if(response.status == 2){

            that.uploadSuccessNum = 0;
            that.uploadFailNum = 0;
            that.uploadTotalNum = 0;

            that.$emit('isLoading', false);

            this.$alert(","+response.Msg, '', {
                confirmButtonText: '',
                callback(){

                }
            })
            return;
          }


          this.uploadSuccessNum ++;

          if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
            // 

            that.finishUpload('');

            return;

          }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
            // 

            that.finishUpload('');
          }
      },
      // 
      uploadErr  (err, file, fileList){
          console.log(err)
          var that = this;

          this.uploadFailNum++;

          if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
            // 

            that.uploadSuccessNum = 0;
            that.uploadFailNum = 0;
            that.uploadTotalNum = 0;
            that.$emit('isLoading', false);

            that.$alert("", '', {
              confirmButtonText: ''
            })
            return;
          }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
            // 

            that.finishUpload('');
          }
      },
      // 
      finishUpload(text){

        var that = this;
        that.$emit('isLoading', false);

        var textvalue = '';
        if(text){
          textvalue = text?","+text:"";
        }
        
        this.$alert(""+textvalue, '', {
            confirmButtonText: '',
            callback(){

              that.$router.back(-1);
            }
        })
      },
      // 
      handlePicturePreview (file) {

          this.dialogImageUrl = file.url;
          this.dialogVisible = true;
      },
      /**
       * 
       */
      commitAbo() {
        var that = this;

        // if(!$("div.uploadImg ul>li").length){

        //   this.$alert('', '', {
        //     confirmButtonText: ''
        //   })

        //   return;
        // }

        this.uploadParams.omsNo = this.omsNo;//
        this.uploadParams.fileType = 'wxAbnormal';// 

        that.uploadTotalNum = $("div.uploadImg ul>li").length;
        console.log(that.uploadTotalNum )

        let params = {
          omsNo: this.OMS.omsNo,
          deliveryNoSF: this.OMS.deliveryNoSF,
          transporterId: this.$store.state.userInfo.orgnizationId,
          transporterName: this.$store.state.userInfo.orgnizationName,
          sponsorName: this.$store.state.userInfo.userName,
          abnormalType: this.aboType,
          abnormalReason: this.aboReason,
          abnormalLink: this.aboLink,
          responsibleParty: this.aboPer,
          abnormalExplain: this.aboText,
        }
        that.$emit('isLoading', true,"");
        
        Axios.post(that.HOST+"reportAbnormal.do",{"params": JSON.stringify(params)})
        .then(function(res){
          console.log(res)
          
          if(res.data.status=='1'){

            if(that.uploadTotalNum>0){
              that.$refs.upload.submit();
            }else{
              that.finishUpload();
            }

          }else{

            that.$emit('isLoading', false);

            var Msg;
            if(res.data.Msg){

              Msg = res.data.Msg;
            }else {

              Msg = "";
            }

            that.$alert(Msg, '', {
              confirmButtonText: '',
            })
          }
        })
        .catch(function(res){

          that.$emit('isLoading', false);
            
          that.$alert("", '', {
            confirmButtonText: ''
          })
        })
      },
      // 
      getAboList() {
        abnormalDropList().then(res => {
          console.log(res)
          this.typeOptions = [] // 
          this.reasonOptions = [] // 
          this.linkOptions = [] // 
          this.perOptions = [] // 
          let obj = JSON.parse(res.data.data)

          for (let i in obj.abnormalLink) { // 
            let iitem = obj.abnormalLink[i]
            let iarr = {}
            iarr.value = iitem
            iarr.label = iitem

            this.linkOptions.push(iarr)
          }
          for (let j in obj.abnormalReason) { // 
            let jitem = obj.abnormalReason[j]
            let jarr = {}
            jarr.value = jitem
            jarr.label = jitem

            this.reasonOptions.push(jarr)
          }
          for (let k in obj.abnormalType) { // 
            let itemk = obj.abnormalType[k]
            let arrk = {}
            arrk.value = itemk
            arrk.label = itemk

            this.typeOptions.push(arrk)
          }
          for (let l in obj.responsibleParty) { // 
            let iteml = obj.responsibleParty[l]
            let arrl = {}
            arrl.value = iteml
            arrl.label = iteml

            this.perOptions.push(arrl)
          }
        })
      },
      // 
      scanData(data) {

        var that = this;

        var postData = {
          scanLabel: data,//
          shipmentCode: that.shipmentCode,//
        };

        console.log(postData)

        this.httpRequest("verificationOmsNo.do",postData,function(res){
          console.log(res);
          
          that.OMS={
            omsNo: res.data.omsNo,
            deliveryNoSF:res.data.deliveryNoSF
          }

          // let obj = res.data;

          // that.OMS = JSON.parse(obj.orderSF);
        })
      },
      goPrev() {
        this.$router.go(-1);
      },
    }
  }
</script>

<style scoped lang="less">
  @import "../../../components/publicLess.less";
.Abnormal{
  font-size: 28/50rem;
  padding-top: 90/50rem;
}
  .form-content{
    padding: 20px;
    background-color: #fff;
    position: relative;
  }
  .form-item {
    margin: 30/50rem 0;
    &>div{
      margin-top: 15/50rem;
    }
  }
  .form-item label {
    display: inline-block;
    font-size: 16px;
    color: #6f7180;
  }
  .footer-commit{
    font-size: 28/50rem;
    text-align: center;
    display: flex;
    width: 100%;
    color: #fff;
  }
  .footer-commit .commit{
    flex: 1;
    line-height: 70/50rem;
    margin: 0 30/50rem 30/50rem;
    background-color: @PublicColor;
    text-align: center;
    border-radius: 35/50rem;
  }
  .uploadImg{
    padding:0 30/50rem 30/50rem;
    box-sizing:border-box;
    div{
      float: none;
    }
    &>div:nth-child(1){
      margin-bottom: 15/50rem;
    }
    span{
      float: none;
      color: #aaa;
    }
  }
</style>



