//http
Vue.prototype.httpRequest=function (url,params,success,isLoading_){

	var that = this;

	if(isLoading_ && isLoading_ == 'isLoading_'){

		that.$emit('isLoading_', true,"");

	}else{

		that.$emit('isLoading', true,"");
	}

	Axios.post(that.HOST+url,{"params": JSON.stringify(params)})
	.then(function(res){
		console.log(res)

		
		if(isLoading_ && isLoading_ == 'isLoading_'){

			that.$emit('isLoading_', false);

		}else{

			that.$emit('isLoading', false);
		}

		if(res.data.status==1 || res.data.type==1){

			success(res.data);

		}else{

			var Msg;
			if(res.data.Msg){

				Msg = res.data.Msg;
			}else {

				Msg = "";
			}

			if(Msg == '!'){
				success(res.data);
				return;
			}

			that.$alert(Msg, '', {
	            confirmButtonText: '',
	            callback: action => {
		            
	            }
           	})
		}
	})
	.catch(function(res){

		if(isLoading_ && isLoading_ == 'isLoading_'){

			that.$emit('isLoading_', false);

		}else{

			that.$emit('isLoading', false);
		}
			
		that.$alert("", '', {
	        confirmButtonText: ''
	   	})
		
	})
}

