Vue.prototype.onPlusReady=function (isLoading_is){

	var that = this;

	if(!window.plus){
		return;
	}

	if(isLoading_is == 'isLoading_'){

		this.$emit(isLoading_is, true,"");

	}else{

		this.$emit('isLoading', true,"");
	}

	plus.geolocation.getCurrentPosition(function(p){
		
		if(p.coords.longitude && p.coords.latitude){

			AMap.plugin('AMap.Geocoder', function() {

			    var geocoder = new AMap.Geocoder({
				    // city adcode  citycode
				    city: '010'
				})
				// 113.863454,22.584533
				// 116.39,39.9  
			  	// var lnglat = ['113.863454', '22.584533']
			  	// var lnglat = '113.863454,22.584533'
			  	var lnglat = [p.coords.longitude, p.coords.latitude]

			  	geocoder.getAddress(lnglat, function(status, result) {

			  		console.log(status)
			  		console.log(result)
			  		if(isLoading_is == 'isLoading_'){

						that.$emit(isLoading_is, false);

					}else{

						that.$emit('isLoading', false);
					}

			    	if (status === 'complete' && result.info === 'OK') {
				        // result
				        that.CurrentCity = result.regeocode.addressComponent.city?result.regeocode.addressComponent.city:result.regeocode.addressComponent.province;
				        that.CurrentLocation = result.regeocode.formattedAddress;

			    	}else{

			    		that.$alert('', '', {
				            confirmButtonText: ''
			           	})
			    	}
			  	})
			})

		}else{
			if(isLoading_is == 'isLoading_'){

				that.$emit(isLoading_is, false);

			}else{

				that.$emit('isLoading', false);
			}
			that.$alert('', '', {
	            confirmButtonText: ''
	       	})
		}
	}, function(e){

		if(isLoading_is == 'isLoading_'){

			that.$emit(isLoading_is, false);

		}else{

			that.$emit('isLoading', false);
		}

		that.$alert(e.message, '', {
            confirmButtonText: ''
       	})
	} );
}


