import axios from 'axios'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded'

// let api = 'http://192.168.2.214:8880/cyscm/tmsApp/' //
let api = 'http://sfkc.cy-scm.com:9998/tmsApp/'

// let api = 'http://sfkc.sf-express.com/tmsApp/'
// let api = 'http://192.168.43.57:8880/cyscm/tmsApp/'

export default api

// 
export const getTheOrderNumber = params => {
  return axios.post(api + `verificationLabel.do`, params)
}
// 
export const addOrderList = params => {
  return axios.post(api + `commitScan.do`, params)
   // scanLabel
}
// 
export const checkUpdate = params => {
  return axios.post(api + `versionUpdate.do`, params)
}

// 
export const abnormalDropList = params => {
  return axios.get(api+ `abnormalSeleteData.do`)
}

// 
export const commitAbnormal = (data, file) => {
  return axios.post(api + `reportAbnormal.do`, data)
}



