<template>
  <div class="MassTransfer">
	<header><i class="iconfont icon-xiangzuo1"  @click="$router.back(-1)"></i><span></span></header>
	<div class="container borderLine">
		<!-- <div><span></span><span>{{shipmentCode}}</span></div> -->
		<div><span></span><span>{{omsNo}}</span></div>
		<div><span></span><span>{{CurrentLocation}}</span></div>
		<div><span></span><span>{{CurrentCity}}</span></div>
	  <div class="Upload">
		<div class="uploadImg">
			<div class="LoadImg_head"></div>
			<div>
				<el-upload :action = " HOST+'confirmationUpload.do'"  :limit="6" :multiple="true"  :data="uploadParams" ref='upload_ref1' :auto-upload="false" list-type="picture-card"  :before-upload="beforeUpload"   :on-error="uploadErr" :on-success="uploadSuccessful"   :on-preview="handlePictureCardPreview" >
				  <i class="el-icon-plus"></i>
				</el-upload>
				<el-dialog :visible.sync="dialogVisible">
				  <img width="100%" :src="dialogImageUrl" alt="">
				</el-dialog>
			</div>
		</div>
	  </div>
	</div>
	<div class="submitBtn">
		<p class="g_signBtn"  @click="submit"></p>
	</div>
	<div v-if="ifTips" class="msg_tips">
		<div class="tips_content">
			<i class="iconfont icon-chenggong1"></i>
			<p>{{tips_Msg}}</p>
		</div>
	</div>
  </div>
</template>
<script type="text/javascript">

	import $ from "jquery"
	
	export default{
		name:"MassTransfer",
		data(){
			return{
				dialogImageUrl: '',
		        dialogVisible: false,//
		        uploadParams:{},// 
		        tips_Msg:"",//
				ifTips:false,//
		        shipmentCode:'',//
		        omsNo:'',//
		        CurrentLocation:'',//
		        CurrentCity:'',//
		        uploadSuccessNum:0,//
		        uploadTotalNum:0,//
		        uploadFailNum:0,//
			}
		},
		created(){

			if(this.$route.query.shipmentCode){
		        this.shipmentCode = this.$route.query.shipmentCode;
		    }

		    if(this.$route.query.omsNo){
		        this.omsNo = this.$route.query.omsNo;
		    }

			this.onPlusReady('e');

			// this.WaybillInfo = this.$store.state.Waybill.WaybillInfo.clickWaybillInfo[0];

			// this.uploadParams.tenantCode = this.WaybillInfo.tenantCode;//

			// this.uploadParams.code = this.$route.query.deliverNo_list;//()

			// this.uploadParams.codeType = "ORDER";// 

			// this.uploadParams.codeId = this.$route.query.deliverNo_list;//=deliveryId,=shipmentId

			this.uploadParams.cellphone = this.$store.state.userInfo.cellphone;//

		},
		methods:{
			// 
			submit(){

				var that = this;

				if(!$(".Upload>div.uploadImg:eq(0) ul>li").length){

					that.$alert('', '', {
			            confirmButtonText: ''
		           	})

		           	return;
				}

				that.uploadTotalNum = $("div.uploadImg ul>li").length;
				console.log(that.uploadTotalNum )

				// 
				this.$refs.upload_ref1.submit();
			},
			// 
			finishUpload(text){

				text = text?","+text:"";

				var that = this;
				this.$alert(""+text, '', {
		            confirmButtonText: '',
		            callback(){

		            	that.$router.back(-1);
		            }
		        })
			},
			// 
		    uploadSuccessful  (response, file, fileList){

		        console.log(response)

		        var that = this;

		        this.uploadSuccessNum ++;

		        if (that.uploadSuccessNum == parseInt(that.uploadTotalNum)) {
		          // 

		          that.finishUpload('');

		          return;

		        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
		          // 

		          that.finishUpload('');
		        }
		    },
		    // 
		    uploadErr  (err, file, fileList){
		        console.log(err)
		        var that = this;

		        this.uploadFailNum++;

		        if (that.uploadFailNum == parseInt(that.uploadTotalNum)) {
		          // 

		          that.uploadSuccessNum = 0;
		          that.uploadFailNum = 0;
		          that.uploadTotalNum = 0;
		          that.$emit('isLoading', false);

		          that.$alert("", '', {
		            confirmButtonText: ''
		          })
		          return;
		        }else if (parseInt(that.uploadFailNum) + parseInt(that.uploadSuccessNum) == parseInt(that.uploadTotalNum)) {
		          // 

		          that.finishUpload('');
		        }
		    },
			// 
			handlePictureCardPreview (file) {

			    this.dialogImageUrl = file.url;
			    this.dialogVisible = true;
			},
		}
	}
</script>
<style lang="less" scoped>
	.MassTransfer{
		overflow: hidden;
		.container{
			overflow: hidden;
			padding-bottom:150/50rem;
			&>div:not(.Upload){
				padding: 20/50rem;
				overflow: hidden;
				span{
					height: 50/50rem;
					line-height: 50/50rem;
				}
			}
			.Upload{
				.uploadImg{
					padding: 20/50rem;
					box-sizing: border-box;
					font-size: 30/50rem;
					border-top: 1/50rem solid #EDEDED;
					overflow: hidden;
					&>.LoadImg_head{
						float: none;
						text-align: center;
						margin-bottom:10/50rem; 
						span{
							color: #aaa;
						}
					}
					&>div{
						float: left;
					}
				}
			}
			
		}
	}
	.btnBox{
	  	width: 120/50rem;
	    height: 50/50rem;
	    line-height: 50/50rem;
	    background-color: #71b900;
	    color: #fff;
	    font-size: 28/50rem;
	    text-align: center;
	    border-radius: 8/50rem;
	    margin: 10/50rem auto;
	}
</style>


