<template>
  <div class="Waybill">
	<header>
	  <div class="currentTask"></div>
	  <div @click="historyTask"  class="historyTask"></div>
	</header>
	<!-- <div class="waybillBox"> -->
	  <div class="SwitchTab">
	    <div :class="{selected_head : LinkStatus == 'Link_PickUp'}"  @click="LinkChoose('Link_PickUp')"><span></span></div>
	    <div :class="{selected_head : LinkStatus == 'Link_Transit'}" @click='LinkChoose("Link_Transit")'><span></span></div>
	    <div :class="{selected_head : LinkStatus == 'Link_ToSend'}" @click='LinkChoose("Link_ToSend")'><span></span></div>
	  </div>
	  <div class="container" style="overflow:scroll;">
	  
	    <div class="topKong"></div>

		<mt-loadmore style="text-align: center; "  topDropText=""   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="" bottomLoadingText="" :top-method="loadTop"  :bottom-method="loadBottom"  ref="loadmore"  :autoFill="autoFill" >

		  <div class="waybillContainer">

			<!--  -->
			<WaybillInfo_template  @fatherMethodloadData='fatherMethodloadData'  @isLoading_="fullscreenLoading"  v-bind:shipmentListData="shipmentListData" v-bind:LinkStatus="LinkStatus" />

			<div v-if="allLoaded && shipmentListData.length " class="nodata_label"></div>
			<div class="kong"></div>
			<div v-if="is_NoData" class="nodata_kong"></div>
		  </div>
		</mt-loadmore>
	  </div>
	<!-- </div> -->
	  
	<!--  -->
	<div   v-if="is_NoData"   class="NoData" >
	  <div>
		<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
		<div>{{is_NoData_text}}</div>
	  </div>
	</div>

	<!--  -->
	<FooterIndex/>
  </div>
</template>
<script type="text/javascript">

	// 
	import FooterIndex from '../../components/footer.vue'

	//  
	import WaybillInfo_template from './_template_WaybillInfo'
	import $ from "jquery"
	export default{
		name:"Waybill",
		data(){
			return{
				shipmentListData:[],
		        is_NoData:false,//  
		        is_NoData_text:"",//  
				numberPerPage: 15,//
				loadPage: 1 ,//
				allLoaded:true,//
				autoFill:false,//
				LinkStatus:"Link_PickUp",//    
			}
		},
		components:{
		  	FooterIndex,
		  	WaybillInfo_template,//
		},
		mounted(){

			var that = this;

			this.allLoaded = false;

		},
		created(){
			
			// 
			if(this.$route.query.LinkStatus){
				this.LinkStatus = this.$route.query.LinkStatus;
			}
			if(this.$store.state.Waybill.waybillLinkStatus){
				this.LinkStatus = this.$store.state.Waybill.waybillLinkStatus;
			}
			this.loadData();
		},
		methods:{
		  // 
		  LinkChoose(LinkStatus){

		  	this.loadPage = 1;
		  	this.allLoaded = false;

		    this.LinkStatus = LinkStatus;

		    this.$store.state.Waybill.waybillLinkStatus = LinkStatus;

		    this.loadData();
		  },
			// 
			loadData(load){

				var that = this;

				var postData = {
					currentPage: that.loadPage,//
				    numberPerPage: that.numberPerPage,//
				    startDate: null,//
				    endDate: null,//
				    deliveryNoinput: null,// 
                    shipmentCodeinput: null,//  
                    clientOrderNo: null,//
				    cellphone: that.$store.state.userInfo.cellphone,//   15013418360
				    transportLinks:that.LinkStatus == "Link_PickUp" ? "":(that.LinkStatus == "Link_Transit" ? "":(that.LinkStatus == "Link_ToSend" ? "":""))
			    };

				this.httpRequest("queryShipmentData.do",postData,function(res){

					if(load){
						if(load == "TopLoad"){

							that.$refs.loadmore.onTopLoaded();

						}else if(load == "BottomLoad"){

							that.$refs.loadmore.onBottomLoaded();
						}
					}

					// 
			        if (that.loadPage > 1) {
				   
				        that.shipmentListData = that.shipmentListData.concat(res.data)
				      
			        } else {
				       
				        that.shipmentListData = res.data;

						if(res.data.length){

							that.is_NoData = false; // 

						}else{

							that.is_NoData_text = ""
							

							that.is_NoData = true;
						}
			        }

			        that.$store.state.Waybill.shipmentListData = that.shipmentListData;

				    // 
				    if (res.data.length < that.numberPerPage) {
				 
				        that.allLoaded = true;
			
				    } else {
				        
				        that.allLoaded = false;
				      
				    }
				})
			},
			// 
			fatherMethodloadData(){
				this.loadData();
			},
			//     
			"fullscreenLoading"(isLoad,loadingText){

				console.log(isLoad,loadingText)

				this.$emit('isLoading', isLoad,loadingText);

			},
			//   
			historyTask(){

				this.$router.push({
		            name:"historyList"
		        })
			},
			// 
			loadTop() {

		        this.loadPage = 1;

		        this.allLoaded = false;

				this.loadData("TopLoad");
			},
			// 
			loadBottom(){

			    var that = this;

			    if (that.allLoaded){
			     	that.$refs.loadmore.onBottomLoaded();
			        return;
			    }else{
			     
			        that.loadPage += 1;
			    }

				this.loadData("BottomLoad");
			}
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
  .Waybill{
	height: 100%;
  }
  header{
	background-color: #fff;
	color: #000;
	display: flex;
	&>div{
	  flex-grow: 1;
	}
	.currentTask{
	  color: @PublicColor;
	  border-bottom: 6/50rem solid @PublicColor;
	}
	.historyTask{
	  border-bottom: 6/50rem solid #fff;
	}
  }
	.SwitchTab{
	  width: 100%;
	  height: 75/50rem;
	  font-size: 32/50rem;
	  position: fixed;
	  top: 91/50rem;
	  left: 0;
	  z-index: 999;
	  background-color: #fff;
	  display: flex;
	  &>div{
		flex-grow: 1;
		height: 70/50rem;
		line-height: 70/50rem;
		text-align: center;
	  }
	  .selected_head>span{
		border-bottom: 5/50rem solid @PublicColor;
		height: 100%;
		display: inline-block;
		color: @PublicColor;
	  }
  	}
  	.container{
	  font-size: 29/50rem;
	  height: 100%;
	  padding-top: 0;
	  margin-top: 0;
	  padding:0 20/50rem 0;
	  background-color:#F4F4F4;
	  	.waybillContainer{

	  	}
	}
  
  .topKong{
    height: 186/50rem;
  }
</style>


