<!-- _template_orderInfo.vue    -->
<template>
    <div class="OrderInfo_template">
		<div class="orderHead" >
			<!-- <span class="selectAll" v-if="!historyList&&(_orderInfo[0]? !_orderInfo[0].notOperate : true)"  @click="selectAll"></span> -->
		</div>
		<div class='goodsInfo'   v-for='(dataItem,index) in _orderInfo'  :id="index"  :key='index' >
			<!--  @click="selectData(index,dataItem.ifDeliver)" -->
		    <div class='goodsHead'>
		    	<!-- <span v-if="dataItem.ifDeliver" class="iconfont icon-xuanzhong"></span> -->
		    	<span v-if=" LinkStatus=='Link_PickUp'" @click='PrintExpress(index)' class="iconfont icon-dayin"><span></span></span>

				<div class="KeyInfoGroup" v-if=" LinkStatus=='Link_ToSend'" >
					<div v-if='chargeTypes==""'><span></span></div>
					<div v-if='returnBillFlag=="T"'><span></span></div>
				</div>

		    	<div class="ordernum">
		    		<span></span>
		    		<span v-clipboard:copy="dataItem.omsNo"
				          v-clipboard:success="onCopy"
				          v-clipboard:error="onError">{{dataItem.omsNo}}
				      <i  v-clipboard:copy="dataItem.omsNo"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>
				    </span>
		    	</div>
		    	<div>
		    		<span>SF</span>
		    		<span v-clipboard:copy="dataItem.clientOrderNo"
				          v-clipboard:success="onCopy"
				          v-clipboard:error="onError">{{dataItem.clientOrderNo}}
				      <i v-if='dataItem.clientOrderNo'  v-clipboard:copy="dataItem.clientOrderNo"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>
				    </span>
		    	</div>
		    	<!-- backBottleFlag  T  -->
		    <!-- 	<div v-if="backBottleFlag != 'T' || dataItem.receivePartyName">
		    		<span></span>
		    		<span>{{dataItem.receivePartyName}}</span>
		    	</div> -->
		    	<div v-if="backBottleFlag != 'T' || (dataItem.receivePartyContactName || dataItem.receivePartyPhone)">
		    		<span></span>
		    		<span>{{dataItem.receivePartyContactName}}&nbsp;&nbsp;&nbsp; {{dataItem.receivePartyPhone}}&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;<i @click="callPhone(dataItem.receivePartyPhone)" class="iconfont icon-dianhua-copy"></i></span>
		    	</div>
		    	<div v-if="backBottleFlag != 'T' || dataItem.receivePartyAddr1">
		    		<span></span>
		    		<span>{{dataItem.receivePartyAddr1}}</span>
		    	</div>
		    <!-- 	<div>
		    		<span></span>
		    		<span>{{dataItem.issuePartyName}}</span>
		    	</div> -->
		    	<div>
		    		<span></span>
		    		<span>{{dataItem.issuePartyContact}}&nbsp;&nbsp;&nbsp; {{dataItem.issuePartyTel}}&nbsp;&nbsp;&nbsp; &nbsp;&nbsp;<i v-if="dataItem.issuePartyTel" @click="callPhone(dataItem.issuePartyTel)" class="iconfont icon-dianhua-copy"></i></span>
		    	</div>
		    	<div>
		    		<span></span>
		    		<span>{{dataItem.issuePartyAddr}}</span>
		    	</div>
		<!--     	<div   v-if="dataItem.send">
			
					    	<span></span>
			<span>{{dataItem.send}}</span>
		</div>
		<div  v-if="dataItem.requestedDeliveryDte">
			<span></span>
			<span>{{dataItem.requestedDeliveryDte}}</span>
		</div> -->
		    	<div>
		    		<div v-if="LinkStatus=='Link_PickUp'">
			    		<span></span>
			    		<span>{{dataItem.actualQty}}</span>
			    	</div>
			    	<div v-if="LinkStatus=='Link_PickUp'">
			    		<span></span>
			    		<span>{{dataItem.actualWt}} &nbsp; KG</span>
			    	</div>
		    	</div>
		    	<div><span></span><span>{{dataItem.valuationMoney}} &nbsp;</span></div>
		    	<div v-if='dataItem.loadingStatus == "WEI" && dataItem.scanNumber'><span></span><span>{{dataItem.scanNumber}} &nbsp;</span></div>
		    	<!-- <div><span></span><span>{{dataItem.loadingStatus == 'YI'?:()}}</span></div> -->
		    </div>

	    	<div v-if='!historyList' class="_btn">

	    		<!--   -->
	    		<span v-if="LinkStatus=='Link_PickUp' && dataItem.loadingStatus == 'WEI'"  @click="ChangeOrderInfo(index)" class="g_btn"></span>


		    	<!--   -->
	    		<span v-if="(LinkStatus=='Link_PickUp' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber < dataItem.actualQty)"  @click="toDeliveryScanXH(index)" class="g_btn"></span>
				
				<!--   -->
	    		<span v-if="(LinkStatus=='Link_PickUp' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber >= dataItem.actualQty)"  class="g_btn g_btn_disab"></span>

				
	    		<!--   -->
	    		<span v-if="(LinkStatus=='Link_Transit' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber < dataItem.actualQty)"  @click="toTransitScan(index)" class="g_btn"></span>

	    		<!--   -->
	    		<span v-if="(LinkStatus=='Link_Transit' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber >= dataItem.actualQty)"  class="g_btn g_btn_disab"></span>

				<!--   -->
	    		<span class="g_btn" v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'WEI'"  @click="submitOrders(index,'')"></span>

	    		<!--   -->
	    		<span v-if="(LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber < dataItem.actualQty)"  @click="toDeliveryOrderPickScan(index)" class="g_btn"></span>

	    		<!--   -->
	    		<span v-if="(LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'WEI')&&(dataItem.scanNumber >= dataItem.actualQty)" class="g_btn g_btn_disab"></span>

	    		<!--   -->
	    		<span v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'YI' && dataItem.signStatus == 'WEI'"  @click="toReceiptScan(index)" class="g_btn"></span>

	    		<!--   -->
	    		<!-- <span class="g_btn" v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'YI' && dataItem.signStatus == 'WEI'"  @click="submitOrders(index,'')"></span> -->


		    	<!--   -->
		    	<span class="g_btn" @click="toAbnormal(index)"></span>


				<!--   -->
		    	<span class="g_btn" v-if="(LinkStatus=='Link_PickUp'||LinkStatus == 'Link_Transit')&& dataItem.loadingStatus == 'WEI'" @click="submitOrders(index,'')"></span>


	    		<!--   -->
	    		<span class="g_btn" v-if="LinkStatus=='Link_ToSend' && dataItem.loadingStatus == 'YI' && returnBillFlag=='T'"  @click="toMassTransfer(index)"></span>
	    	</div>
		    	
		    <div class='goodsContent' @click="detail_takaUp(index)" :style="{display: backBottleFlag == 'T' ? 'block' : 'none'}">
			    <div v-for='(Item,idx) in dataItem.deliveryDetailList' :id="idx" :key='idx'>{{Item.productNo}} &nbsp;&nbsp;&nbsp;  {{Item.productName}}&nbsp;&nbsp;&nbsp;      {{Item.poOrderQty}}{{Item.poUom}}</div>
		    </div>
		    <div class="goodsBtn" v-if='dataItem.deliveryDetailList.length'>
		    	<div class="TakeUp" @click="detail_takaUp(index)">
			    	<div><span>{{backBottleFlag == 'T' ? '' : ''}}</span><i  class="iconfont icon-up" :style="{transform : backBottleFlag == 'T' ? 'rotate(180deg)' : 'rotate(0deg)'}"></i></div>
			    </div>
		    	<!-- <span v-if="dataItem.ifDeliver" class="g_btn" @click="toOrderDeliver(index)"></span>
		    	<span v-if="!historyList"  @click="_ewm(dataItem.omsNo)" class="g_btn ewm"></span> -->
		    </div>
		   <!--  <div v-if=" (_orderInfo[0]? !_orderInfo[0].notOperate : true) && !historyList"  class="g_doubleBtn bottomBtn">
		   	            <div v-if="!selectAddr" @click="toSelectAdd('ArriveReceivingPoint')"></div>
		   	            <div @click="toOrderDeliver('selectAll')"></div>
		   	            <div v-if="!selectAddr" @click="toSelectAdd('deliveryAddress')"></div>
		   	        </div> -->
		</div>
		<div v-if="ifTips" class="msg_tips msg_tips1">
			<div class="tips_content">
				<i class="iconfont icon-chenggong1"></i>
				<span>{{tips_Msg}}</span>
			</div>
		</div>

	    <!--  -->
	    <div class="BatchCommitPopBox" v-show='ChangeInfo_show'>
	      <div class="BatchCommit_">
	        <div class="BatchCommit_head"></div>
	        <div class="BatchCommit_ChangeInfo">
	          <div>
	          	<div><span></span></div>
	          	<div><input type="number" v-model='ChangeOrderQty'></div>
	          	<div><span></span></div>
	          </div>
	          <div>
	          	<div><span></span></div>
	          	<div><input type="number" v-model='ChangeOrderWt'></div>
	          	<div><span>KG</span></div>
	          </div>
	        </div>
	        <div class="BatchCommit_btn">
	          <div class="commit-button" @click='ChangeInfoCancel'></div>
	          <div class="commit-button" @click='ChangeInfoConfirm'></div>
	        </div>
	      </div>
	    </div>
    </div>
</template>

<script>
	import $ from "jquery"

export default {
    name:"OrderInfo_template",
    props:{
		_orderInfo:{
    		type:Array,
	    	default: () => {
				return []
			}
    	},
    	shipmentID:0,
    	backBottleFlag:'',//
    	shipStatus:'',//
    	confirmNumOfDriverReceive:'',//
    	selectAddr:false,//
    	historyList:false,//
		LinkStatus:'',//

    },// 
    data(){
	    return{
	    	// _orderInfo:[],//

			tips_Msg:"",//
			ifTips:false,
			ChangeInfo_show:false,//  
			ChangeOrderIndex:0,// idx
			ChangeOrderQty:'',// 
			ChangeOrderWt:'',// 
	    }
    },
    mounted(){

    },
    created(){
    	console.log(this.LinkStatus)
    },
    methods:{
    	// 
    	PrintExpress(index){
    		var that = this;

    		var postData = {
				omsNo: that._orderInfo[index].omsNo,//
		    };
		    console.log(postData)

			this.httpRequest("surfacePrint.do",postData,function(res){
				console.log(res);

				var omsNo =  JSON.stringify(res.data);

				try{
	    			var main = plus.android.runtimeMainActivity();
		            var Intent = plus.android.importClass("android.content.Intent");
		            var intent = new Intent(main.getIntent());
		            intent.setClassName(main, "com.kaidongyuan.app.kdydriver.ui.activity.PrintActivity");
		            intent.setFlags(Intent.FLAG_ACTIVITY_CLEAR_TOP);
		            intent.putExtra("omsNo", omsNo);
		            main.startActivity(intent);
	    		}
	    		catch(error) {
	    			alert(error)
				}
			},'isLoading_')
    	},
		//   
		toDeliveryScanXH (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"DeliveryScanXH",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					transportLinks:that.LinkStatus,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		//  
		ChangeOrderInfo(index){

		  this.ChangeOrderIndex = index;// idx

		  this.ChangeOrderQty = this._orderInfo[index].actualQty,// 
		  this.ChangeOrderWt = this._orderInfo[index].actualWt,// 
		  this.ChangeInfo_show = true;
		},
		//  
		ChangeInfoCancel(){
			this.ChangeInfo_show = false;
		},
		//  
		ChangeInfoConfirm(){

			var that = this;

			if(!that.ChangeOrderQty || !that.ChangeOrderWt){
				return;
			}

			that.ChangeInfo_show = false;
	        var postData = {
	        	omsNo: that._orderInfo[that.ChangeOrderIndex].omsNo,//
	            deliveryId: that._orderInfo[that.ChangeOrderIndex].deliveryId,//id
	            orderQty: that.ChangeOrderQty,//
	            orderWt: that.ChangeOrderWt,//
	        };

	        console.log(postData)

	        that.httpRequest("updateOrderQty.do",postData,function(res){
	          console.log(res)

	          that.$alert('', '', {
	            confirmButtonText: '',
	            callback(){
	              that.$parent.loadData();
	            }
	          })
	        },'isLoading_')
		},
		// 
		submitOrders(index,text){
			event.stopPropagation();
			var that = this;

			this.$confirm(""+text+" "+ that._orderInfo[index].omsNo , '', {
	          confirmButtonText: '',
	          cancelButtonText: '',
	          type: 'warning'
	        }).then(() => {

		        var linkNode = ''; // linkNode

		        if(that.LinkStatus == 'Link_PickUp'){
		        	linkNode = '10';
		        }
		        if(that.LinkStatus == 'Link_Transit'){
		        	linkNode = '20';
		        }
		        if(that.LinkStatus == 'Link_ToSend' && that._orderInfo[index].loadingStatus == 'WEI'){
		        	linkNode = '30';
		        }
		        if(that.LinkStatus == 'Link_ToSend' && that._orderInfo[index].loadingStatus == 'YI'){
		        	linkNode = '40';
		        }

	        	var postData = {
					deliveryId:that._orderInfo[index].deliveryId,//id
					transportLinks:linkNode,//
					driverName:that.$store.state.userInfo.userName,//
				}

				console.log(postData)

				that.httpRequest( "updateDeliveryScanNumber.do",postData,function(res){
					console.log(res)

					that.$alert('', '', {
			            confirmButtonText: '',
			            callback: action => {
				            that.$parent.loadData();
			            }
		           	})
				},"isLoading_")
	          
	        }).catch(() => {
	          // this.$message({
	          //   type: 'info',
	          //   message: ''
	          // });          
	        });
		},
		//   
		toTransitScan (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"TransitScan",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		//   
		toDeliveryOrderPickScan (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"DeliveryOrderPickScan",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		//   
		toAbnormal (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"Abnormal",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryNoSF:that._orderInfo[index].clientOrderNo
				}
			})
		},	
		//   
		toReceiptScan (index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"ReceiptScan",
				query:{
					omsNo : that._orderInfo[index].omsNo,
					deliveryId:that._orderInfo[index].deliveryId,
					scanNumber:that._orderInfo[index].scanNumber,
					actualQty:that._orderInfo[index].actualQty
				}
			})
		},
		//   
		toMassTransfer(index){

			event.stopPropagation();
			
			var that = this;

			this.$router.push({
				name:"MassTransfer",
				query:{
					omsNo : that._orderInfo[index].omsNo
				}
			})

		},
    	//  
    	toSelectAdd(BtnToUse){

    		event.stopPropagation();

    		var that = this;

    		if(BtnToUse == "ArriveReceivingPoint"){

				that.shipmentID = that.$store.state.Waybill.WaybillInfo.clickShipmentId;

				var postData = {
					shipmentId: that.shipmentID,//id
			    };

				that.httpRequest("queryDeliverReceiveAddrInfo.do",postData,function(res){

					if(res.data.length == 1){

						console.log(res.data[0].receivePartyAddr1)

						that.$router.push({
							name:"ArrivalConfirm",
							query:{
								receiveAddr:res.data[0].receivePartyAddr1,//
							}
						})
					}else{
						that.$router.push({
							name:"SelectAddress",
							query:{
								BtnToUse:BtnToUse
							}
						})
					}
				},'isLoading_')

    		}else{
    			// 

    			console.log(this.confirmNumOfDriverReceive)

    			if(this.confirmNumOfDriverReceive == 'F'){

    				that.$alert("", '', {
				        confirmButtonText: ''
				   	})
				   	return;
    			}
    			this.$router.push({
					name:"SelectAddress",
					query:{
						BtnToUse:BtnToUse
					}
				})
    		}
    	},
    	// 
    	// arriveDestination(){

    	// },
		//  
		toOrderDeliver(index){

			event.stopPropagation();

			var that = this;

			if(this.confirmNumOfDriverReceive == 'F' && that.shipStatus != ''){

				that.$alert("", '', {
			        confirmButtonText: ''
			   	})
			   	return;
			}

			var deliverNo_list = [];

			var validateArray = [];//

			// 
			if(index != "selectAll"){

				deliverNo_list[0] = that._orderInfo[index].deliveryId;

				that.$router.push({
					name:"orderDeliver",
					query:{
						deliverNo_list:deliverNo_list,
						selectAddr:that.selectAddr
					}
				})

				return;

			// 
			}else{

				for(var i = 0 ; i < that._orderInfo.length;i++){

					if($(".goodsInfo").eq(i).children(".goodsHead").children("span.iconfont").hasClass("icon-xuanzhong")){

						deliverNo_list[deliverNo_list.length] = that._orderInfo[i].deliveryId;

						validateArray[validateArray.length] = that._orderInfo[i].receivePartyContactName;
					}
				}

				if(!deliverNo_list.length){

					that.$alert("", '', {
				        confirmButtonText: ''
				   	})

				}else{

					that.$router.push({
						name:"orderDeliver",
						query:{
							deliverNo_list:deliverNo_list,
							selectAddr:that.selectAddr
						}
					})
				}
			}
		},
		//
		selectData:function(idx,ifDeliver){

			var that = this;

			if(!ifDeliver){
				return;
			}

			var iconElement = $(".goodsInfo").eq(idx).children(".goodsHead").children("span.iconfont");
			
			if(iconElement.hasClass("icon-xuanzhong")){

				iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong")

			}else if(iconElement.hasClass("icon-weixuanzhong")){

				iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
			}

			var if_Element;
			var if_changeBtn = {
				Selected:false,
				unSelected:false
			};
			for(var n = 0; n < that._orderInfo.length; n++){

				if_Element = $(".goodsInfo").eq(n).children(".goodsHead").children("span.iconfont");

				if(if_Element.hasClass("icon-xuanzhong")){

					if_changeBtn.Selected = true;

				}else if(if_Element.hasClass("icon-weixuanzhong")){

					if_changeBtn.unSelected = true;

				}
			}

			if(if_changeBtn.Selected != if_changeBtn.unSelected){

				if($(".selectAll").html() == ""){

					$(".selectAll").html("");

				}else{
					$(".selectAll").html("");
				}
			}
		},
		// 
		selectAll(){

			console.log(this._orderInfo)
			// console.log(1)
			// console.log(this._orderInfo[0])
			var that = this;

			if($(".selectAll").html() == ""){

				$(".selectAll").html("");

				var iconElement;

				for(var j = 0; j < that._orderInfo.length; j++){

					iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

					iconElement.removeClass("icon-xuanzhong").addClass("icon-weixuanzhong");
				}

			}else{

				$(".selectAll").html("");

				var iconElement;

				for(var j = 0; j < that._orderInfo.length; j++){

					iconElement = $(".goodsInfo").eq(j).children(".goodsHead").children("span.iconfont");

					iconElement.removeClass("icon-weixuanzhong").addClass("icon-xuanzhong")
				}
			}
		},
		// 
		detail_takaUp(index){
			event.stopPropagation();

			var that = this;

			var this_span = $(".goodsInfo").eq(index).find(".TakeUp").find("span");

			var this_icon = $(".goodsInfo").eq(index).find(".TakeUp").find("i");

			if(this_span.html() == ""){

				$(".goodsInfo").eq(index).find(".goodsContent").show();

				this_span.html(""); 

				this_icon.css("transform","rotate(180deg)");

			}else{

				$(".goodsInfo").eq(index).find(".goodsContent").hide();

				this_span.html("");

				this_icon.css("transform","rotate(0deg)");
			}
		},
		// 
		_ewm(omsNo){

			this.$router.push({
				name:"QRCode",
				query:{
					omsNo:omsNo
				}
			})
		},
		onCopy (){

			var that = this;
			that.ifTips = true;
			that.tips_Msg = "";

			setTimeout(function(){

				that.ifTips = false;
			},1000)
		},
    }
}
</script>

<style lang="less" scoped>
@import "../../../components/publicLess.less";
.orderHead{
	text-align: center;
	height: 65/50rem;
	line-height: 65/50rem;
	color: #000;
	background-color: #EDEFF3;
	position:relative;
	span{
		height: 65/50rem;
		position:absolute;
		left: 20/50rem;
		top: 0;
	}
}
.goodsInfo{
	color: #777;
	padding:0 16/50rem;
	box-sizing: border-box;
  	border-top: 8/50rem solid #EDEFF3;
  	background-color: #fff;
  	.goodsHead{
  		padding-top: 10/50rem;
  		overflow: hidden;
  		position:relative;
  		line-height: 55/50rem;
  		border-bottom: 3/50rem solid #EDEFF3;
  		&>div:not(.KeyInfoGroup){
  			overflow: hidden;
  			&.ordernum{
  				i{
  					color: @PublicColor;
  					font-size: 37/50rem;
  					margin-left: 20/50rem;
  				}
  			}
  			span{
  				float: left;
  				height: auto;

		    	&:nth-child(2){
					width: 500/50rem;
		    	}
  			}
  			&>span:nth-child(1){
  				color:#000;
  			}
  			&>div{
				float: left;
				width: 50%;
				span{
	  				float: static;
	  				height: auto;

			    	&:nth-child(2){
						width:auto;
			    	}
	  			}
	  			&>span:nth-child(1){
	  				color:#000;
	  			}
  			}
  		}
  		.KeyInfoGroup{
			position: absolute;
			top: 20/50rem;
			right: 0;
			display: flex;
			&>div{
				width: auto;
				padding:0/50rem 10/50rem;
				border-radius:10/50rem;
				color: @PublicColor;
				border:2/50rem solid @PublicColor;
				font-weight: 700;
				margin-right: 20/50rem;
				&:last-child{
					margin-right: 0;
				}
			}
  		}
  		// &>div:not(:last-child){
  		// 	span:nth-child(1){
  		// 		width: 170/50rem;
  		// 	}
  		// }
  		&>span.icon-xuanzhong{
  			position:absolute;
  			top: 20/50rem;
  			right: 20/50rem;
			color:@PublicColor;
			font-size: 40/50rem;
  		}
  		&>span.icon-dayin{
  			position:absolute;
  			top: 20/50rem;
  			right: 10/50rem;
  			color: @PublicColor;
  			font-size: 36/50rem;
  			span{
  				font-size: 28/50rem;
  				margin-left: 10/50rem;
  			}
  		}
  	}
  	.goodsBtn{
  		overflow: hidden;
  		.TakeUp{
  			float: left;
	  		text-align: center;
	  		height: 60/50rem;
	  		line-height: 60/50rem;
	  		overflow: hidden;
	  		div{
	  			color: @PublicColor;
	  			overflow: hidden;
	  			display: inline-block;
	  			height: 80/50rem;
	  			margin: 0 auto;
	  		}
	  		span{
	  			float: left;
	  		}
	  		i{
	  			float: left;
	  			font-size: 50/50rem;
	  		}
	  	}
		span{
			float: right;
			height:60/50rem;
			line-height: 60/50rem;
			width: 200/50rem;
		}
		span.ewm{
			margin-right: 15/50rem;
		}
  	}
  	._btn{
  		margin-top:15/50rem; 
  		overflow: hidden;
  		display: flex;
		justify-content : flex-end;
		span{
	  		margin-bottom:15/50rem; 
			height:60/50rem;
			line-height: 60/50rem;
			// max-width: 35%;
			width: 25%;
			margin-left: 10/50rem;
		}
  	}
}

// 
span.g_btn{
	border-radius:10/50rem;
	color:#fff;
	background-color: #6897FF;
	text-align: center;
}
span.g_btn_disab{
	background-color:#aaa;
}
.bottomBtn{
	width: 100%;
	height: 90/50rem;
	position: fixed;
	display: flex;
	bottom: 0;
	div{
		flex-grow: 1;
		border-radius: 35/50rem;
		margin-right: 15/50rem;
		margin-left: 15/50rem;
	}
}
.BatchCommitPopBox{
	.BatchCommit_ChangeInfo{
		&>div{
			width: 100%;
			display: flex;
			height: 80/50rem;
			line-height: 80/50rem;
			&>div{
				&:nth-child(1){
					width: 120/50rem;
					text-align: center;
					font-size: 30/50rem;
					flex-shrink: 0;
				}
				&:nth-child(2){
					flex-grow: 1;
					input{
						width: 100%;
						height: 60/50rem;
						border-radius: 30/50rem;
						text-align: center;
						font-size: 30/50rem;
					}
				}
				&:nth-child(3){
					width: 80/50rem;
					text-align: center;
					font-size: 27/50rem;
					flex-shrink: 0;
				}
			}
		}
	}
}
.commit-button{
	height: 70/50rem;
	line-height: 70/50rem;
	font-size: 30/50rem;
	margin-top: 30/50rem;
	margin-bottom: 10/50rem;
	background-color: #038EEA;
	border-radius: 35/50rem;
	flex: 1;
	text-align: center;
	color: #fff;
}
.BatchCommit{
	margin-right: 10/50rem;
}
</style>


