<template>
  <div class="historyList">
	<header>
	  <div  @click="currentTask"  class="currentTask"></div>
	  <div class="historyTask"><i class="iconfont icon-huabanfuben"  @click="search('History')"  ></i></div>
	</header>
	<div class="SwitchTab">
	    <div :class="{selected_head : LinkStatus == 'Link_PickUp'}"  @click="LinkChoose('Link_PickUp')"><span></span></div>
	    <div :class="{selected_head : LinkStatus == 'Link_Transit'}" @click='LinkChoose("Link_Transit")'><span></span></div>
	    <div :class="{selected_head : LinkStatus == 'Link_ToSend'}" @click='LinkChoose("Link_ToSend")'><span></span></div>
	  </div>
	<div class="container" style="overflow:scroll">
	  <div class="topKong"></div>
	  <div  class="search_Label" v-if='history_SearchData.startDate||history_SearchData.endDate||history_SearchData.orderNoinput||history_SearchData.shipmentCodeinput||history_SearchData.TransportLink'>
		<div @click="searchLabel_close('startDate')"  v-if="history_SearchData.startDate"><span></span><i class="iconfont icon-guanbi" ></i></div>
		<div @click="searchLabel_close('endDate')"  v-if="history_SearchData.endDate"><span></span><i class="iconfont icon-guanbi" ></i></div>
		<div @click="searchLabel_close('orderNoinput')"  v-if="history_SearchData.orderNoinput"><span></span><i class="iconfont icon-guanbi" ></i></div>
		<div @click="searchLabel_close('shipmentCodeinput')"  v-if="history_SearchData.shipmentCodeinput"><span></span><i class="iconfont icon-guanbi" ></i></div>
		<div @click="searchLabel_close('TransportLink')"  v-if="history_SearchData.TransportLink"><span></span><i class="iconfont icon-guanbi" ></i></div>
	</div>
	<mt-loadmore style="text-align: center; "  topDropText=""   topLoadingText=""  topPullText='' bottomPullText="" bottomDropText="" bottomLoadingText="" :top-method="loadTop_history"  :bottom-method="loadBottom_history"  ref="loadmore"  :autoFill="autoFill" >
		<div class="waybillContainer">
			<div   class="bigList" v-for='(dataItem,index) in shipmentListData'  :id="index"  :key='index'>
				<!--   -->
				<div  class="place">
					<span class="OrderNum"
						v-clipboard:copy="dataItem.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError">{{dataItem.shipmentCode}}</span>
					<i  v-clipboard:copy="dataItem.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError"
				        class="iconfont icon-fuzhi" ></i>
					<i @click="_ewm(dataItem.shipmentCode)" class="iconfont icon-iconfonterweima" ></i>
					<!-- 
					@click='copy(dataItem.shipmentCode)' -->
					<!--  v-clipboard:copy="dataItem.shipmentCode"
				        v-clipboard:success="onCopy"
				        v-clipboard:error="onError" -->
					<!-- <span  class="LicensePlate" v-if="dataItem.backBottleFlag == 'T'"></span> -->
				</div>
				<!--  -->
				<div  class="searchResult"  @click="toOrderDeliver(dataItem)">
					
					<!--  -->
					<!-- <div  class="firstLine"> -->
					<!-- 	<span>{{dataItem.startCity}}</span>
						<i class="iconfont icon-jiang" ></i>
						<span>{{dataItem.endCity}}</span> -->
						<!-- <span class="dateLoading">{{dataItem.addDate}}</span> -->
					<!-- </div> -->
					<div><span></span><span class="dateLoading">{{dataItem.addDate}}</span></div>
					<div>
						<span>&nbsp;&nbsp;&nbsp;</span>
						<span>{{dataItem.countDelivery}}  &nbsp; </span>
						<span>{{(dataItem.vol * 0.000001).toFixed(1)}}  &nbsp; </span>
						<span>{{(dataItem.wt * 0.000001).toFixed(1)}}  &nbsp; </span>
						<!-- <span v-if='dataItem.upFlag == "T" && dataItem.caseQty'>{{parseInt(dataItem.caseQty)}}</span> -->
						<!-- <span v-if='dataItem.upFlag != "T"'>{{parseInt(dataItem.qty)}}</span> -->
						<!-- <span v-if="dataItem.upFlag == 'T' &&dataItem.trayQty">{{parseInt(dataItem.trayQty)}}</span> -->
					</div>
					
					<div v-if="dataItem.deliveryTime">
						<span></span>
						<span>{{dataItem.deliveryTime}}</span>
					</div>
					<div v-if="dataItem.issueAddrLiat.length==1">
						<span></span>
						<span>{{dataItem.issueAddrLiat[0].issueAddr1}}</span>
					</div>
					<div v-if="dataItem.issueAddrLiat.length==1">
						<span></span>
						<span>{{dataItem.issueAddrLiat[0].issueUser}}&nbsp;&nbsp;&nbsp; {{dataItem.issueAddrLiat[0].issuePhone}}</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<i v-if="dataItem.issueAddrLiat[0].issuePhone" @click="callPhone(dataItem.issueAddrLiat[0].issuePhone)" class="iconfont icon-dianhua-copy" ></i>
					</div>
					<div   v-if="dataItem.issueAddrLiat.length !=1 &&dataItem.issueAddrLiat.length !=0" @click="MultiAddress(index)">
						<i class="iconfont icon-tishi" ></i>
						<span style="color:#038EEA; "></span>
					</div>
					<div class="detailBtn">
						<span  class="g_btn" @click="toOrderDeliver(dataItem)"></span>
				    	<span  class="g_btn" @click="ViewTrajectory(dataItem.shipmentID)"></span>
				    </div>
				</div>
			</div>
			<div v-if="allLoaded && shipmentListData.length " class="nodata_label"></div>
			<div class="kong"></div>
			<div v-if="is_NoData" class="nodata_kong"></div>
		</div>
	</mt-loadmore>
	<!--  -->
	<div   v-if="is_NoData"   class="NoData" >
		<div>
			<i  class="iconfont icon-meiyouwuliuxinxi" ></i>
			<div>{{is_NoData_text}}</div>
		</div>
	</div>
	<!--  -->
	<div class="MultiAddress">
		<div>
			<i @click="closeAddress"  class="iconfont icon-iconfontcuowu" ></i>
			<div class="addressHead">
				<span  @click="address_SwitchView(idx)"  v-for='(itemAddHead,idx) in address_listData'  :id="idx"  :key='idx' >{{idx+1}}</span>
			</div>
			<div class="addressContent">
				<div>
					<span></span>
					<span>{{currentAddressInfo.issueAddr1}}</span>
				</div>
				<div>
					<span></span>
					<span>{{currentAddressInfo.issueUser}}</span>
				</div>
				<div>
					<span></span>
 					<span>{{currentAddressInfo.issuePhone}}</span> &nbsp;&nbsp;&nbsp;&nbsp;  <i v-if="currentAddressInfo.issuePhone" style="color:#038EEA; " @click="callPhone(currentAddressInfo.issuePhone)" class="iconfont icon-dianhua-copy" ></i>
				</div>
				<div v-if="currentAddressInfo.phoneAppointTime">
					<span></span>
					<span>{{currentAddressInfo.phoneAppointTime}}</span>
				</div>
				<div v-if="currentAddressInfo.phoneAppointTime">
					<span></span>
					<span>{{currentAddressInfo.appointLoadingStatus}}</span>
				</div>
			</div>
		</div>
	  </div>
	</div>
	<div v-if="ifTips" class="msg_tips msg_tips1">
		<div class="tips_content">
			<i class="iconfont icon-chenggong1"></i>
			<span>{{tips_Msg}}</span>
		</div>
	</div>
	<FooterIndex/>
  </div>
</template>
<script type="text/javascript">
import FooterIndex from '../../components/footer.vue'
	export default{
		name:"historyList",
		data(){
			return{
				LinkStatus:"Link_PickUp",//    
				shipmentListData:[],//
				address_listData:[],//  shipmentListData[i].issueAddrLiat
				currentAddressInfo:[],// 
				history_NoSearchFrom:true,// false
				is_NoData:false,//  
		        is_NoData_text:"",//  
		        numberPerPage: 15,//
				loadPage: 1 ,//
				allLoaded:false,//
				autoFill:false,//
				// 
				history_SearchData:{
					startDate: null,//
				    endDate: null,//
				    orderNoinput: null,// 
	                shipmentCodeinput: null,//  
	                TransportLink: null,//
				},
				thisMonth:"",//\
				tips_Msg:"",//
				ifTips:false
			}
		},
		components:{
			FooterIndex
		},
		created(){

			var that = this;

			// 
			if(this.$route.query.LinkStatus){
				this.LinkStatus = this.$route.query.LinkStatus;
			}

			if(this.$store.state.history_LinkStatus){
				this.LinkStatus = this.$store.state.history_LinkStatus;
			}

			// false
			this.history_NoSearchFrom = that.$store.state.history_NoSearchFrom;

			// 
			if(!that.$store.state.history_NoSearchFrom){

				this.history_SearchData = this.$store.state.history_SearchData;
			}

			this.loadData();
		},
		// 
		beforeDestroy(){

			var that = this;

			// that.$store.state.history_NoSearchFrom = true;
		},
		methods:{
			// 
		    LinkChoose(LinkStatus){

			  	this.loadPage = 1;
			  	this.allLoaded = false;

			    this.LinkStatus = LinkStatus;

			    this.$store.state.history_LinkStatus = LinkStatus;

			    this.loadData();
		    },
			// 
			loadData(load){

				var that = this;

				var history_Data = that.$store.state.history_Data;//
				var postData;

				// 
				if(!that.$store.state.history_NoSearchFrom){

					postData = that.$store.state.history_SearchData;

					postData.transportLinks = that.LinkStatus == "Link_PickUp" ? "":(that.LinkStatus == "Link_Transit" ? "":(that.LinkStatus == "Link_ToSend" ? "":""));

				}else{

					postData = {
					    startDate: null,//
					    endDate: null,//
					    orderNoinput: null,// 
                        shipmentCodeinput: null,//  
                        TransportLink: null,//
					    cellphone: that.$store.state.userInfo.cellphone,//   15013418360
					    transportLinks:that.LinkStatus == "Link_PickUp" ? "":(that.LinkStatus == "Link_Transit" ? "":(that.LinkStatus == "Link_ToSend" ? "":""))
				    };
				}

				postData.currentPage = that.loadPage;//
				postData.numberPerPage = that.numberPerPage;//

				this.httpRequest("queryOldShipmentData.do",postData,function(res){

					if(load){
						if(load == "TopLoad"){

							that.$refs.loadmore.onTopLoaded();

						}else if(load == "BottomLoad"){

							that.$refs.loadmore.onBottomLoaded();
						}
					}

					// 
			        if (that.loadPage > 1) {
				   
				        that.shipmentListData = that.shipmentListData.concat(res.data)
				      
			        } else {
				       
				        that.shipmentListData = res.data;

						if(res.data.length){

							that.is_NoData = false; // 

						}else{

							that.is_NoData_text = ""
							

							that.is_NoData = true;
						}
			        }

			        that.$store.state.history_Data = that.shipmentListData;

				    // 
				    if (res.data.length < that.numberPerPage) {
				 
				        that.allLoaded = true;
			
				    } else {
				        
				        that.allLoaded = false;
				      
				    }
				})
				
			},
			//   
			currentTask(){

				this.$router.push({
		            name:"Waybill"
		        })
			},
			// 
			searchLabel_close(searchName){

				this.$store.state.history_SearchData[searchName] = null;

				this.loadData();
			},
			// 
			ViewTrajectory(shipmentID){

				event.stopPropagation();

				this.$router.push({
					name:"TrackOnTheWay",
					query:{
						shipmentID : shipmentID,
						head:''
					}
				})
			},
			//   
			toOrderDeliver(WaybillInfo){

				this.$store.state.search_WaybillDetail = WaybillInfo;
				var that = this;

				this.$router.push({
					name:"WaybillDetail",
					query:{
						shipmentID:WaybillInfo.shipmentID,
						LinkStatus:that.LinkStatus
					}
				})
			},
			// 
			_ewm(shipmentCode){
				event.stopPropagation();

				this.$router.push({
					name:"QRCode",
					query:{
						shipmentCode:shipmentCode,//
					}
				})
			},
			onCopy (){

				var that = this;
				that.ifTips = true;
				that.tips_Msg = "";

				setTimeout(function(){

					that.ifTips = false;
				},1000)
			},
			// 
			loadTop_history() {

		        this.loadPage = 1;

		        this.allLoaded = false;

				// 
				this.$store.state.history_needRefresh = true;

				this.loadData("TopLoad");
			},
			// 
			loadBottom_history(){

			    var that = this;

			    if (that.allLoaded){
			     	that.$refs.loadmore.onBottomLoaded();
			        return;
			    }else{
			     
			        that.loadPage += 1;
			    }

				// 
				this.$store.state.history_needRefresh = true;

				this.loadData("BottomLoad");
			}
		}
	}
</script>
<style lang="less" scoped>
  @import "../../components/publicLess.less";
	.historyList{
		height: 100%;
		header{
			background-color: #fff;
			color: #000;
			display: flex;
			&>div{
				flex-grow: 1;
			}
			.currentTask{
				border-bottom: 6/50rem solid #fff;
			}
			.historyTask{
				color: @PublicColor;
				border-bottom: 6/50rem solid @PublicColor;
				position: relative;
				i{
					position: absolute;
					top: 5/50rem;
					right: 20/50rem;
				}
				i.iconfont::before{
				  font-size:40/50rem;
				}
			}
		}
		.SwitchTab{
		  width: 100%;
		  height: 75/50rem;
		  font-size: 32/50rem;
		  position: fixed;
		  top: 91/50rem;
		  left: 0;
		  z-index: 999;
		  background-color: #fff;
		  display: flex;
		  &>div{
			flex-grow: 1;
			height: 70/50rem;
			line-height: 70/50rem;
			text-align: center;
		  }
		  .selected_head>span{
			border-bottom: 5/50rem solid @PublicColor;
			height: 100%;
			display: inline-block;
			color: @PublicColor;
		  }
	  	}
		.container{
			padding-top: 0;
			margin-top: 0;
			font-size: 29/50rem;
			padding:0 20/50rem 0;
			background-color:#F4F4F4;
			height: 100%;
			.waybillContainer{
				background-color:#F4F4F4;
			}
			.bigList{
				text-align: left;
				background-color: #fff;
				padding:10/50rem 20/50rem;
				margin-bottom: 20/50rem;
				&>div{
					overflow: hidden;
					position: relative;
					width: 100%;
					padding: 20/50rem 0;
					box-sizing: border-box;
					&>div{
						// height: 55/50rem;
						line-height: 55/50rem;
						overflow: hidden;
					}
					.iconfont{
						font-size: 35/50rem;
						color: @PublicColor;
						margin-right: 5/50rem;
					}
				}
				.OrderNum{
					margin-right: 20/50rem;
				}
				&>div.place{
					overflow: hidden;
					border-bottom: 1/50rem solid #EDEFF3;
					&>div{
						height: 70/50rem;
				  		line-height: 70/50rem;
						overflow: hidden;
					}
					span{
						float: left;
					}
					span.LicensePlate{
						position: absolute;
						top: 20/50rem;
						right: 20/50rem;
						background-color: #00C800;
						padding:0  25/50rem;
						height: 50/50rem;
						line-height: 50/50rem;
						color: #fff;
						border-radius:25/50rem;
					}
					&>i:nth-child(3){
						float: right;
					}
				}
				&>div.searchResult{
					padding-bottom: 0;
					.firstLine{
						position:relative;
						overflow: hidden;
						margin-top: 20/50rem;
						.dateLoading{
							float: right;
							color: #777;
							font-size: 27/50rem;
						}
						i{
							margin:0 20/50rem;
						}
					}
					.detailBtn{
						height: 60/50rem;
						margin-top: 10/50rem;
						span{
							float: right;
							height:50/50rem;
							line-height: 50/50rem;
							width: 180/50rem;
							border-radius:10/50rem;
							color:#fff;
							background-color: #6897FF;
							text-align: center;
							margin-right: 20/50rem;
						}
						span:nth-child(1){
							margin-right: 0;
						}
					}
				}
			}
		}
	}
	// 
 .topKong{
    height: 186/50rem;
  }
</style>


