<template>
  <div class="coc">
    <div class="common-box">
      <v-header :headerName="headerName" :orderList="orderList"></v-header>
      <div class="content-box">
        <v-scan @scanData="scanData" :switchScan="switchScan" :loadingData="loadingData"></v-scan>
      </div>
    </div>
    <div class="order-level level2">
      <el-tabs v-model="activeName" :stretch="true" v-loading="loadingData">
        <el-tab-pane :label="'(' + orderList.length + ')'" name="first">
          <div class="order-header">
            ({{ orderList.length }})
          </div>
          <div class="order-list">
            <div class="list-item" v-for="item in orderList">
              {{item.order}}
              <!--<div class="close-button" @click="deleteItem(item.order)"></div>-->
            </div>
          </div>
          <div class="no-data" v-show="orderList.length == 0">
            <div class="right-bg"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane :label="'('+ historyLabelNo.length + ')'" name="second">
          <div class="order-list">
            <div class="list-item" v-for="item in historyLabelNo">
              {{ item }}
            </div>
          </div>
          <div class="no-data" v-show="historyLabelNo.length == 0">
            <div class="right-bg"></div>
          </div>
        </el-tab-pane>
        <el-tab-pane :label="'(' + notScanLabelNo.length + ')'" name="third">
          <div class="order-list">
            <div class="list-item" v-for="item in notScanLabelNo">
              {{ item }}
            </div>
          </div>
          <div class="no-data" v-show="notScanLabelNo.length == 0">
            <div class="right-bg"></div>
          </div>
        </el-tab-pane>
      </el-tabs>
    </div>
    <v-footer
      :count="orderList"
      :orderCount="orderCodeList.filter(item => {return item !== ''}).length"
      :countData="countData"
      :cacheResponseData="cacheResponseData"
      @reflash="reflashData"
      @scanFun="switchScanFun"
      :orderList="cleanOrderList">
    </v-footer>
    <v-confirm ref="message"></v-confirm>

    <!---->
    <!--<input class="testInput" type="text" v-model="test">-->

    <!--ver 1.1.4 -->
    <el-dialog title=""
               :visible.sync="showSliderData"
               :close-on-click-modal="false"
               :show-close="false"
               width="90%" class="search-side">
      <el-form ref="form" :model="scanResult" label-width="80px" label-position="top">
        <el-form-item label="">
          <el-input v-model="scanResult.issuePartyAddr" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.receivePartyAddr1" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.orderQty" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.orderWt" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.displayWt" placeholder="">
            <template slot="append">KG</template>
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showSearchResult"> </el-button>
        <el-button type="primary" @click="confirmResult"> </el-button>
      </span>
    </el-dialog>

    <!--ver 1.1.4 -->
    <el-dialog title=""
               :visible.sync="showSliderData01"
               :close-on-click-modal="false"
               :show-close="false"
               width="90%" class="search-side">
      <el-form ref="form" :model="scanResult" label-width="80px" label-position="top">
        <el-form-item label="">
          <el-input v-model="scanResult.issuePartyAddr" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.receivePartyAddr1" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.orderQty" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.orderWt" :disabled="true" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="scanResult.displayWt" placeholder="">
            <template slot="append">KG</template>
          </el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmResult01"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import Header from '@/components/header'
  import Scan from '@/components/Scan'
  import CommitFooter from '@/components/commitFooter'
  import Confirm from '@/components/confirm'
  import {getTheOrderNumber} from "../../../api/api";
  export default {
    name: "delivery-scan",
    components: {
      'v-header': Header, // 
      'v-scan': Scan,  // 
      'v-footer': CommitFooter,  // 
      'v-confirm': Confirm
    },
    watch: {
    },
    data() {
      return {
        test: '',
        activeName: 'first',
        headerName: '()',
        orderCount: 0, // 
        countData: [], // 
        orderList: [], // 
        orderCodeList: [], // 
        cleanOrderList: [], // 

        scanLabelNo: [], // 
        historyLabelNo: [], // 
        notScanLabelNo: [], // 

        loadingData: false,
        linkNode: '',
        showSliderData: false, // 
        showSliderData01: false,
        scanResult: {
          issuePartyAddr: '--',
          receivePartyAddr1: '--',
          orderQty: '--',
          orderWt: '--',
          displayWt: '--'
        }, // 
        propScanData: '',
        responseData: {}, // 
        switchScan: true,
        testData: {}, //
        cacheResponseData: [] // 
      }
    },
    // http://localhost:8082/#/DeliveryScan
    mounted() {
      switch (this.$route.path) {
        case '/DeliveryScanXH':
          this.linkNode = '10'
          break
        case '/DeliveryScan':
          this.linkNode = '10'
          break
        case '/TransitScan':
          this.linkNode = '20'
          break
        case '/DeliveryOrderPickScan':
          this.linkNode = '30'
          break
        case '/ReceiptScan':
          this.linkNode = '40'
          break
        default:
          this.linkNode = '10'
          break
      }
    },
    created(){
      
      if(this.$route.query.shipmentCode){
        this.shipmentCode = this.$route.query.shipmentCode;
      }
    },
    methods: {
      /**
       *  true  false 
       */
      switchScanFun(data) {
        if(data) {
          this.switchScan = true // 
        }else {
          this.switchScan = false // 
        }
      },
      showSearchResult() {
        if(this.switchScan) {
          this.switchScan = false  //
        }else {
          this.switchScan = true // 
        }
        if(!this.showSliderData) {
          this.showSliderData = true
        }else {
          this.showSliderData = false
        }
      },
      // 
      userBehaviorFun() {},
      /**
       * 
       **/
      deleteItem(key) {
        this.orderList = this.orderList.filter(item => {
          return item.order !== key
        })
        let arr = []
        for (let i in this.orderList) {
          arr.push(this.orderList[i]['orderBooting'])
        }

        // 
        function unique(list) {
          return Array.from(new Set(list))
        }

        this.orderCodeList = unique(arr)
        this.cleanOrderList = []
        for (let item in this.orderList) {
          this.cleanOrderList.push(this.orderList[item].order)
        }
      },
      /**
       * 
       **/
      reflashData() {
        this.countData = []
        this.orderList = []
        this.orderCodeList = []
        this.cleanOrderList = []

        this.scanLabelNo= [] // 
        this.historyLabelNo= [] // 
        this.notScanLabelNo= []// 
      },
      /**
       * {scan.vue}
       * @param data 
       */
      scanData(data) {
        let _this = this
        // if(this.orderList.length !== 0) {
        //   if(JSON.stringify(this.orderList).indexOf(JSON.stringify(data)) == -1) {
        //     let params = {
        //       scanLabel: data
        //     }
        //     this.getTheOrderNum(params, data)
        //   }else {
        //     alert('')
        //   }
        // }else {
        //   let params = {
        //     scanLabel: data
        //   }
        //   this.getTheOrderNum(params, data)
        // }

        // 
        if (this.historyLabelNo.length === 0 && this.notScanLabelNo.length === 0 && this.orderList.length === 0) {
          let params = {
            scanLabel: data,
            linkNode: this.linkNode,
            shipmentCode:_this.shipmentCode
          }
          this.getTheOrderNum(params, data)
        } else { // 
          _this.step0(data).then(data0 => { // 
            if(data0.length !== 0) {
              _this.$refs.message.show('!', {
                type: 'alert',
                confirmText: '',
                titleText: '',
              })
            }else {
              _this.step1(data).then(data1 => {
                if (data1.length !== 0) { // 
                  _this.$refs.message.show('!', {
                    type: 'alert',
                    confirmText: '',
                    titleText: '',
                  })
                } else {
                  _this.step2(data).then(data2 => {
                    if (data2.length !== 0) { // 

                      for(let key in _this.cacheResponseData) {
                        if(_this.cacheResponseData[key].allOrder.indexOf(data) !== -1) {
                        _this.scanResult = _this.cacheResponseData[key]
                        }
                      }
                      this.showSliderData01 = true // 
                      this.switchScan = false // 

                      this.orderList.push({
                        order: data,
                        orderBooting: '',
                        time: this.getTheTimeNow()
                      })
                      // 
                      this.notScanLabelNo = this.notScanLabelNo.filter(item => {
                        return item !== data
                      })
                    } else {
                      let params = {
                        scanLabel: data,
                        linkNode: this.linkNode,
                        shipmentCode:_this.shipmentCode
                      }
                      this.getTheOrderNum(params, data)
                    }
                  })
                }
              })
            }
          })
        }
      },
      /**
       * 
       **/
      getTheTimeNow() {
        const date = new Date()
        var year = date.getFullYear()
        var month = checkVal(date.getMonth() + 1)
        var day = checkVal(date.getDay())
        var hour = checkVal(date.getHours())
        var min = checkVal(date.getMinutes())
        var sec = checkVal(date.getSeconds())

        function checkVal(val) {
          if (val < 10) {
            return '0' + val
          } else {
            return val
          }
        }

        return `${year}-${month}-${day} ${hour}:${min}:${sec}`
      },
      /**
       * 
       **/
      distinct(a, b) {
        let arr = a.concat(b)
        let result = []
        let obj = {}

        for (let i of arr) {
          if (!obj[i]) {
            result.push(i)
            obj[i] = 1
          }
        }
        return result
      },
      // 0. 
      step0(val) {
        let _this = this
        return new Promise(resolve => {
          let arr = _this.orderList.filter(item => {
            return item.order == val
          })
          resolve(arr)
        })
      },
      // 1. 
      step1(val) {
        let _this = this
        return new Promise((resolve, reject) => {
          let arr = _this.historyLabelNo.filter(item => {
            return item == val
          })
          resolve(arr)
        })
      },
      // 2. 
      step2(val) {
        let _this = this
        return new Promise((resolve, reject) => {
          let arr = _this.notScanLabelNo.filter(item => {
            return item == val
          })
          resolve(arr)
        })
      },
      /**
       * { getTheOrderNumber }
       * @param params {  }data {  }
       */
      getTheOrderNum(params, data) {
        let _this = this
        this.loadingData  = true
        this.propScanData = data
        getTheOrderNumber(params).then(res => {
          console.log(res)
          if (res.data.status === '1') { // 1
            this.loadingData  = false
            let obj = JSON.parse(res.data.data)
            //
            this.responseData = obj
            // 
            this.scanResult = JSON.parse(JSON.parse(res.data.data).orderSF)
            this.scanResult.allOrder = (JSON.parse(res.data.data).historySubmitLabelNo).concat((JSON.parse(res.data.data).notScanLabelNo))
            // ''''
            this.showSearchResult()

            // orderSF
            if(this.cacheResponseData.length !== 0) { // 
              // 
              for(let key in this.cacheResponseData) {
                if (this.scanResult.omsNo === this.cacheResponseData[key].omsNo) {
                  this.scanResult.omsNo = this.cacheResponseData[key].displayWt
                }
              }
            }else { // 
              this.cacheResponseData.push(this.scanResult)
            }

            // this.orderCodeList = this.distinct(this.orderCodeList, obj.omsNo)
            // this.orderList.push({
            //   order: data,
            //   orderBooting: res.data.omsNo,
            //   time: this.getTheTimeNow()
            // })

            // { orderCodeList }{ distinct }
          } else {
            alert(res.data.Msg)
            this.loadingData  = false
          }
        }).catch(err => {
          alert(err)
          this.loadingData  = false
        })
      },
      /**
       * 
       **/
      confirmResult() {
        this.showSearchResult()
        // 
        this.dealConfirmData(this.propScanData)
        this.$refs['form'].validate((valid) => {
          if(valid) {
            for(let key in this.cacheResponseData) {
              // 
              if(this.cacheResponseData[key].omsNo === this.scanResult.omsNo) {
                this.cacheResponseData[key].displayWt = this.scanResult.displayWt
              }
            }
          }
        })
      },
      confirmResult01() {
        this.showSliderData01 = false // 
        this.switchScan = true // 

        this.$refs['form'].validate((valid) => {
          if(valid) {
            for(let key in this.cacheResponseData) {
              // 
              if(this.cacheResponseData[key].omsNo === this.scanResult.omsNo) {
                this.cacheResponseData[key].displayWt = this.scanResult.displayWt
              }
            }
          }
        })
        // alert(JSON.stringify(this.cacheResponseData))
      },
      /**
       * ,
       */
      dealConfirmData(data) {
        const _this = this
        const obj = this.responseData
        function reSetData() {
          return new Promise(resolve => {
            // 
            _this.historyLabelNo = _this.historyLabelNo.concat(obj.historySubmitLabelNo)
            _this.notScanLabelNo = _this.notScanLabelNo.concat(obj.notScanLabelNo)
            // 
            function unique(list) {
              return Array.from(new Set(list))
            }
            _this.historyLabelNo = unique(_this.historyLabelNo)
            _this.notScanLabelNo = unique(_this.notScanLabelNo)
            resolve(true)
          })
        }

        reSetData().then(resolve => {
          if(resolve) {
            // 
            _this.step1(data).then(data1 => {
              if (data1.length !== 0) { // 1. 
                _this.$refs.message.show('!', {
                  type: 'alert',
                  confirmText: '',
                  titleText: '',
                })
                this.orderCodeList = this.distinct(this.orderCodeList, obj.omsNo)
              } else {
                _this.step2(data).then(data2 => { // 2. 
                  if (data2.length !== 0) { // 
                    this.orderList.push({
                      order: data,
                      orderBooting: obj.omsNo,
                      time: this.getTheTimeNow()
                    })
                    // 
                    this.notScanLabelNo = this.notScanLabelNo.filter(item => {
                      return item !== data
                    })
                    this.orderCodeList = this.distinct(this.orderCodeList, obj.omsNo)
                  } else { // 
                    _this.$refs.message.show('!', {
                      type: 'alert',
                      confirmText: '',
                      titleText: '',
                    })
                  }
                })
              }
            })
          }
        })
        this.cleanOrderList = []
        for (let item in this.orderList) {
          this.cleanOrderList.push(this.orderList[item].order)
        }
      }
    }
  }
</script>

<style scoped>

</style>



