import Vue from "vue"
import vuex from "vuex"

Vue.use(vuex);

export default new vuex.Store({
	state:{
		userInfo:[],// 

		typeOfCarList:[],// 

		// 
		Waybill:{
			shipmentListData:[],//  
			// address_listData:[],// shipmentListData[i].issueAddrLiat
			// currentAddressInfo:{},//
			waybillLinkStatus:'Link_PickUp',// 

			// 
			WaybillInfo:{
				clickWaybillInfo:[],//
				clickShipmentId:"",//id
			}
		},
		// WaybillData

		Waybill_needRefresh:true,// 
		IF_Navigation:true,// 
		
		deliveryList:[],// 
		Device:"",//iOS, android

		history_Data:[],//
		history_NoSearchFrom:true,// 
		history_SearchData:{},// 
		history_needRefresh:true,// 
		search_WaybillDetail:[],// 
		history_LinkStatus:'Link_PickUp',// 

		ScanQRCodeInputName : "",  //
		WX_Openid:'',// Openid
		App_Version:'',//App
		driverShipmentInfo:[], //shipment

		flashStatus: false,
		scan: false
	},
	mutations: {
	    activeScan(state) {
	      state.scan = true
	    },
	    offScan(state) {
	      state.scan = false
	    }
	}
})


